// src/context/utils/enemyUtils.js
import { ACTION_TYPES } from '../actions';
import { DUNGEONS } from '../../data/dungeons';

/**
 * Create enemy utility functions
 * @param {Object} state - Current game state
 * @param {Function} dispatch - Reducer dispatch function
 * @param {Object} utils - Utility functions
 * @returns {Object} Enemy utility functions
 */
const createEnemyUtils = (state, dispatch, utils) => {
  const { log, gainXP } = utils;

  // 先に enemyUtils オブジェクトを宣言
  const enemyUtils = {};

  /**
   * 通常モンスターを生成する
   * @param {number} level - ダンジョンレベル
   * @param {Object} dungeonData - ダンジョンデータ
   * @returns {Object} 生成されたモンスター
   */
  enemyUtils.generateRegularMonster = (level, dungeonData) => {
    const availableMonsters = dungeonData.monsters.filter(
      m => level >= m.levelRange[0] && level <= m.levelRange[1]
    );

    if (availableMonsters.length === 0) {
      console.error(`レベル${level}で出現可能なモンスターが見つかりません`);
      return null;
    }

    const template = availableMonsters[Math.floor(Math.random() * availableMonsters.length)];
    
    return {
      name: template.name,
      hp: template.baseHp + (level - template.levelRange[0]) * 3,
      maxHp: template.baseHp + (level - template.levelRange[0]) * 3,
      attack: template.baseAttack + Math.floor((level - template.levelRange[0]) / 2),
      defense: template.baseDefense + Math.floor((level - template.levelRange[0]) / 3),
      xp: template.xp,
      gold: template.gold,
      isBoss: false
    };
  };

  /**
   * Generate a monster based on dungeon level
   * @returns {Object} Generated monster object
   */
  enemyUtils.generateMonster = () => {
    const level = state.dungeon.level;
    const dungeonData = DUNGEONS[state.dungeon.type];
    
    if (!dungeonData) {
      console.error('ダンジョンデータが見つかりません');
      return null;
    }

    // ボス階層の場合、ボスモンスターを生成（ただし、既に倒したボスは出現させない）
    if (dungeonData.bossLevels.includes(level)) {
      const bossData = dungeonData.bosses[level];
      if (!bossData) {
        console.error(`レベル${level}のボスデータが見つかりません`);
        return null;
      }
      
      // 既にこのダンジョンを攻略済みの場合はボスを出現させない
      const dungeonCompleted = state.character.completedDungeons.includes(state.dungeon.type);
      if (dungeonCompleted) {
        // 代わりに通常モンスターを生成
        return enemyUtils.generateRegularMonster(level, dungeonData);
      }
      
      return {
        name: bossData.name,
        hp: bossData.baseHp,
        maxHp: bossData.baseHp,
        attack: bossData.baseAttack,
        defense: bossData.baseDefense,
        xp: bossData.xp,
        gold: bossData.gold,
        isBoss: true,
        description: bossData.description,
        drops: bossData.drops
      };
    }

    // 通常モンスターの生成
    return enemyUtils.generateRegularMonster(level, dungeonData);
  };
  
  /**
   * Create and spawn an enemy in the dungeon
   * @returns {Object} The spawned monster
   */
  enemyUtils.spawnEnemy = () => {
    const monster = enemyUtils.generateMonster();
    dispatch({
      type: ACTION_TYPES.UPDATE_DUNGEON,
      payload: { 
        enemyPresent: true, 
        currentEnemy: monster 
      }
    });
    return monster;
  };
  
  /**
   * Process enemy defeat
   */
  enemyUtils.defeatEnemy = () => {
    const enemy = state.dungeon.currentEnemy;
    
    if (!enemy) {
      console.error('Enemy data is undefined');
      return;
    }
    
    const xpReward = enemy.xp || 0;
    const goldReward = enemy.gold || 0;
    
    // 経験値とゴールドの獲得を先に処理
    if (gainXP && xpReward > 0) {
      gainXP(xpReward);
      log(`${xpReward} の経験値を獲得！`, 'success');
    }
    
    if (goldReward > 0) {
      dispatch({
        type: ACTION_TYPES.UPDATE_CHARACTER,
        payload: {
          gold: state.character.gold + goldReward
        }
      });
      log(`${goldReward} ゴールドを獲得！`, 'success');
    }

    // クエスト進行
    if (enemy.isBoss) {
      utils.updateQuestObjectivesByTrigger('defeat_boss', { 
        monsterName: enemy.name, 
        dungeonType: state.dungeon.type 
      });
    } else {
      utils.updateQuestObjectivesByTrigger('defeat_monster', {
        monsterName: enemy.name,
            dungeonType: state.dungeon.type 
      });
    }

    // 敵の表示を消し、通常モンスターの場合はclearedRoomsをインクリメント
    dispatch({
      type: ACTION_TYPES.UPDATE_DUNGEON,
      payload: {
        enemyPresent: false,
        currentEnemy: null,
        clearedRooms: enemy.isBoss ? state.dungeon.clearedRooms : state.dungeon.clearedRooms + 1
      }
    });
    
    // 敵を倒した通知
    log(`${enemy.name}を倒した！ (経験値: ${xpReward}, ゴールド: ${goldReward})`);
    
    // ボス撃破時は必ずダンジョン攻略済みの処理を実行
    if (enemy.isBoss) {
      utils.completeDungeon(state.dungeon.type);
    }
    
    // Clear any special event if this was a rare monster
    if (state.specialEvent && state.specialEvent.type === 'rareMonster') {
      dispatch({ type: ACTION_TYPES.CLEAR_SPECIAL_EVENT });
    }
  };

  /**
   * Handle successful negotiation with an enemy
   * @param {number} divisor - XP divisor for negotiation reward
   */
  enemyUtils.negotiateSuccess = (divisor = 4) => {
    const enemy = state.dungeon.currentEnemy;
    
    if (!enemy) return;
    
    const xpGain = Math.floor((enemy.xp || 0) / divisor);
    
    // 経験値の獲得
    if (gainXP) {
      gainXP(xpGain);
    }
    
    dispatch({
      type: ACTION_TYPES.UPDATE_DUNGEON,
      payload: {
        enemyPresent: false,
        currentEnemy: null,
        clearedRooms: state.dungeon.clearedRooms + 1
      }
    });

    // Clear any special event if this was a rare monster
    if (state.specialEvent && state.specialEvent.type === 'rareMonster') {
      dispatch({ type: ACTION_TYPES.CLEAR_SPECIAL_EVENT });
    }
  };

  return enemyUtils;
};

export default createEnemyUtils;
