// src/components/quests/QuestBoard.js
import React, { useState } from 'react';
import { useGame } from '../../context/GameContext';
import { findItemById } from '../../data/itemDatabase';

/**
 * 利用可能なクエストを表示するボードコンポーネント
 * 街の掲示板などで使用する
 * @param {Object} props
 * @param {Function} props.onBack - 任意の「戻る」ボタン用コールバック
 */
const QuestBoard = ({ onBack }) => {
    const { state, startQuest, getAvailableQuests, getQuestGiver, getLocationName } = useGame();
    const [selectedQuest, setSelectedQuest] = useState(null);

    // 利用可能なクエストを取得
    const availableQuests = getAvailableQuests ? getAvailableQuests() : [];

    // クエストを選択
    const handleSelectQuest = (quest) => {
        setSelectedQuest(quest);
    };

    // クエストを受注
    const handleAcceptQuest = () => {
        if (selectedQuest) {
            startQuest(selectedQuest.id);
            setSelectedQuest(null);
        }
    };

    return (
        <div className="bg-indigo-900 rounded-lg shadow-lg overflow-hidden">
            {/* ヘッダー部分は親コンポーネントで処理するので省略 */}

            <div className="p-4">
                {selectedQuest ? (
                    <div className="bg-indigo-800 rounded-lg p-4">
                        <div className="flex justify-between items-start mb-3">
                            <h3 className="text-xl font-semibold text-sky-300">{selectedQuest.title}</h3>
                            <div className="flex items-center space-x-2">
                                <div className="text-xs bg-indigo-700 px-2 py-1 rounded">
                                    {selectedQuest.type === 'tutorial' ? 'チュートリアル' :
                                        selectedQuest.type === 'combat' ? '戦闘' : '探索'}
                                </div>
                                {selectedQuest.requiredLevel > 1 && (
                                    <div className="text-xs bg-amber-700 px-2 py-1 rounded">
                                        Lv.{selectedQuest.requiredLevel}+
                                    </div>
                                )}
                            </div>
                        </div>

                        <p className="text-indigo-200 mb-4">{selectedQuest.description}</p>

                        {/* 依頼者情報表示 */}
                        {selectedQuest.giver && (
                            <div className="mb-4 p-3 bg-indigo-900/50 rounded-lg">
                                <h4 className="text-sm font-medium mb-2 text-purple-300">依頼者</h4>
                                <div className="flex items-center">
                                    {(() => {
                                        const giver = getQuestGiver(selectedQuest.giver);
                                        return (
                                            <div>
                                                <p className="font-medium text-white">{giver.name}</p>
                                                <p className="text-xs text-indigo-300">{giver.title} - {getLocationName(giver.location)}</p>
                                            </div>
                                        );
                                    })()}
                                </div>
                            </div>
                        )}

                        <div className="mb-4">
                            <h4 className="text-sm font-medium mb-2 text-sky-200">目標:</h4>
                            <ul className="space-y-2">
                                {selectedQuest.objectives.map(objective => (
                                    <li key={objective.id} className="flex items-start text-sm text-indigo-200">
                                        <div className={`w-3 h-3 mt-1 mr-2 rounded-full ${'bg-indigo-600'}`}></div>
                                        <div>
                                            {objective.description}
                                            {objective.count && (
                                                <div className="text-xs mt-1">
                                                    個数: {objective.count}
                                                </div>
                                            )}
                                        </div>
                                    </li>
                                ))}
                            </ul>
                        </div>

                        <div className="mb-4">
                            <h4 className="text-sm font-medium mb-2 text-sky-200">報酬:</h4>
                            <ul className="text-sm space-y-1">
                                {selectedQuest.rewards.map((reward, index) => (
                                    <li key={index} className="text-amber-300">
                                        {reward.type === 'gold' && `${reward.amount} ゴールド`}
                                        {reward.type === 'xp' && `${reward.amount} 経験値`}
                                        {reward.type === 'item' && `アイテム: ${findItemById(reward.id)?.name || reward.id}`}
                                    </li>
                                ))}
                            </ul>
                        </div>

                        <div className="flex justify-between">
                            <button
                                onClick={() => setSelectedQuest(null)}
                                className="text-sm px-4 py-2 rounded bg-indigo-700 hover:bg-indigo-600 text-white"
                            >
                                戻る
                            </button>

                            <button
                                onClick={handleAcceptQuest}
                                className="text-sm px-4 py-2 rounded bg-green-600 hover:bg-green-700 text-white"
                            >
                                クエスト受注
                            </button>
                        </div>
                    </div>
                ) : (
                    <div>
                        {availableQuests.length > 0 ? (
                            <div className="space-y-3">
                                <p className="mb-3 text-indigo-200">利用可能なクエストを表示しています。クエストを選択して詳細を確認してください。</p>

                                {availableQuests.map(quest => {
                                    // 依頼者情報を取得
                                    const questGiver = quest.giver ? getQuestGiver(quest.giver) : null;

                                    return (
                                        <div
                                            key={quest.id}
                                            onClick={() => handleSelectQuest(quest)}
                                            className="bg-indigo-800 hover:bg-indigo-700 p-3 rounded-lg cursor-pointer transition-colors"
                                        >
                                            <div className="flex justify-between items-center">
                                                <h4 className="font-medium text-sky-300">{quest.title}</h4>
                                                <div className="flex items-center space-x-2">
                                                    <div className="text-xs bg-indigo-700 px-2 py-1 rounded">
                                                        {quest.type === 'tutorial' ? 'チュートリアル' :
                                                            quest.type === 'combat' ? '戦闘' : '探索'}
                                                    </div>
                                                    {quest.requiredLevel > 1 && (
                                                        <div className={`text-xs px-2 py-1 rounded ${
                                                            state.character.level >= quest.requiredLevel
                                                                ? 'bg-amber-700 text-white'
                                                                : 'bg-red-800 text-red-200'
                                                        }`}>
                                                            Lv.{quest.requiredLevel}+
                                                        </div>
                                                    )}
                                                </div>
                                            </div>

                                            <p className="text-indigo-200 text-sm mt-1">{quest.description}</p>

                                            {/* 依頼者情報表示（簡易版） */}
                                            {questGiver && (
                                                <div className="flex items-center mt-2 text-xs text-purple-300">
                                                    <span>依頼者: {questGiver.name}</span>
                                                    <span className="mx-1">-</span>
                                                    <span>{questGiver.title}</span>
                                                </div>
                                            )}
                                        </div>
                                    );
                                })}
                            </div>
                        ) : (
                            <div className="text-center p-6">
                                <p className="text-indigo-300 mb-2">利用可能なクエストはありません</p>
                                <p className="text-indigo-400 text-sm">他のクエストをクリアするか、レベルを上げて戻ってきてください</p>
                            </div>
                        )}
                    </div>
                )}
            </div>
        </div>
    );
};

export default QuestBoard;
