// src/pages/DungeonSelectionPage.js
import { useState } from 'react';
import {useLocation, useNavigate} from 'react-router-dom';
import { useGame } from '../context/GameContext';
import ResponsivePageLayout from '../components/layout/ResponsivePageLayout';
import EquipmentMenu from '../components/town/EquipmentMenu';
import SaveLoadMenu from '../components/game/SaveLoadMenu';
import { difficultyStars } from '../utils/uiHelpers';
import { DUNGEONS } from '../data/dungeons';

const DungeonSelectionPage = () => {
  const { state, selectDungeon } = useGame();
  const [showSaveMenu, setShowSaveMenu] = useState(false);
  const [selectedDungeon, setSelectedDungeon] = useState(null);
  const navigate = useNavigate();
  const location = useLocation();

  const { character } = state;
  const completedDungeons = character.completedDungeons || [];

  // 利用可能なダンジョンの配列を作成
  const dungeons = Object.entries(DUNGEONS).map(([type, data]) => ({
    ...data,
    type
  }));

  // ダンジョン選択処理
  const handleSelectDungeon = (dungeon) => {
    setSelectedDungeon(dungeon);
  };

  // ダンジョン探索開始
  const handleStartExploration = () => {
    if (!selectedDungeon) return;

    const success = selectDungeon(selectedDungeon.id);
    if (success) {
      navigate('/game');
    }
  };

  // Handle quest navigation
  const handleQuestsNavigation = () => {
    sessionStorage.setItem('questReturnPath', location.pathname);
    navigate('/quests');
  };

  // Action buttons for header
  const actionButtons = (
      <>
          <button
              onClick={() => navigate('/town')}
              className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded transition-colors min-w-[90px] md:min-w-[120px]"
          >
              街へ帰還
          </button>
          <button
              onClick={() => handleQuestsNavigation()}
              className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded transition-colors min-w-[90px] md:min-w-[120px]"
          >
              クエスト
          </button>
          <button
              onClick={() => setShowSaveMenu(true)}
              className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded transition-colors min-w-[90px] md:min-w-[120px]"
          >
              メニュー
          </button>
      </>
  );

    // Sidebar content
    const sidebarContent = (
        <EquipmentMenu/>
    );

    return (
        <>
            <ResponsivePageLayout
                title="ダンジョン選択"
                actions={actionButtons}
                sidebar={sidebarContent}
        >
          <div className="bg-indigo-900 rounded-lg shadow-lg p-4">
            <h3 className="text-xl font-semibold mb-4 text-sky-300">利用可能なダンジョン</h3>

            {selectedDungeon ? (
                <div className="mb-4">
                  <div className="bg-indigo-800 p-4 rounded-lg mb-4">
                    <div className="flex justify-between items-start mb-2">
                      <h4 className="font-semibold text-lg text-sky-300">
                        <span className="align-middle">{selectedDungeon.name}</span>
                        {completedDungeons.includes(selectedDungeon.type) && (
                            <span className="align-middle ml-2 text-xs bg-green-600 px-2 py-1 rounded">クリア済み</span>
                        )}
                      </h4>
                      <span className="text-yellow-300">
                    {difficultyStars(selectedDungeon.difficulty)}
                  </span>
                    </div>

                    <p className="text-indigo-200 mb-4">{selectedDungeon.description}</p>

                    <div className="grid grid-cols-2 gap-4 mb-4 text-sm">
                      <div>
                        <span className="text-indigo-300">推奨レベル:</span>
                        <span className={`ml-2 ${character.level < selectedDungeon.minLevel ? 'text-red-400' : 'text-white'}`}>
                      {selectedDungeon.minLevel}-{selectedDungeon.maxLevel}
                    </span>
                      </div>
                      <div>
                        <span className="text-indigo-300">最大階層:</span>
                        <span className="ml-2 text-white">{selectedDungeon.maxLevel}</span>
                      </div>
                      <div>
                        <span className="text-indigo-300">ボス:</span>
                        <span className="ml-2 text-red-300">
                      {selectedDungeon.bossLevels.map(level =>
                          selectedDungeon.bosses[level]?.name
                      ).join(', ') || '???'}
                    </span>
                      </div>
                      <div>
                        <span className="text-indigo-300">状態:</span>
                        <span className="ml-2 text-green-300">
                      {completedDungeons.includes(selectedDungeon.type) ? 'クリア済み' : '未攻略'}
                    </span>
                      </div>
                    </div>

                    {/* 探索開始ボタン */}
                    <div className="flex justify-between items-center">
                      <button
                          onClick={() => setSelectedDungeon(null)}
                          className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded-lg transition-colors"
                      >
                        他のダンジョンを見る
                      </button>

                      <button
                          onClick={handleStartExploration}
                          disabled={character.level < selectedDungeon.minLevel}
                          className={`py-2 px-6 rounded-lg ${
                              character.level < selectedDungeon.minLevel
                                  ? 'bg-red-600 hover:bg-red-700 text-white opacity-50'
                                  : 'bg-green-600 hover:bg-green-700 text-white'
                          }`}
                      >
                        探索開始
                      </button>
                    </div>

                    {character.level < selectedDungeon.minLevel && (
                        <div className="mt-4 p-2 bg-red-900/50 rounded text-xs text-center">
                          このダンジョンには最低レベル{selectedDungeon.minLevel}が必要です。
                          より多くの経験値を集めて戻ってきてください。
                        </div>
                    )}
                  </div>
                </div>
            ) : (
                <>
                  <p className="mb-4">
                    探索するダンジョンを選択してください。各ダンジョンには推奨レベルと報酬があります。
                  </p>

                  <div className="space-y-3">
                    {dungeons.map(dungeon => {
                      const isCompleted = completedDungeons.includes(dungeon.type);
                      const isTooLow = character.level < dungeon.minLevel;

                      return (
                          <div
                              key={dungeon.type}
                              className="bg-indigo-800 hover:bg-indigo-700 rounded-lg p-4 cursor-pointer transition-colors"
                              onClick={() => handleSelectDungeon(dungeon)}
                          >
                            <div className="flex justify-between items-start mb-2">
                              <h4 className="font-semibold text-lg text-sky-300">
                                <span className="align-middle">{dungeon.name}</span>
                                {isCompleted && (
                                    <span className="align-middle ml-2 text-xs bg-green-600 px-2 py-1 rounded">クリア済み</span>
                                )}
                              </h4>
                              <div className="flex items-center space-x-2">
                          <span className="text-xs bg-indigo-700 px-2 py-1 rounded">
                            Lv.{dungeon.minLevel}-{dungeon.maxLevel}
                          </span>
                                <span className="text-yellow-300">
                            {difficultyStars(dungeon.difficulty)}
                          </span>
                              </div>
                            </div>

                            <p className="text-indigo-200 mb-2 text-sm">{dungeon.description}</p>

                            {isTooLow && (
                                <div className="text-red-400 text-xs mt-1">
                                  レベル{dungeon.minLevel}以上が必要です
                                </div>
                            )}
                          </div>
                      );
                    })}
                  </div>
                </>
            )}
          </div>
        </ResponsivePageLayout>

        {/* セーブ/ロードメニュー */}
        {showSaveMenu && (
            <SaveLoadMenu onClose={() => setShowSaveMenu(false)} />
        )}
      </>
  );
};

export default DungeonSelectionPage;
