// src/pages/GamePage.js
import { useEffect, useState } from 'react';
import {useLocation, useNavigate} from 'react-router-dom';
import { useGame } from '../context/GameContext';
import ResponsivePageLayout from '../components/layout/ResponsivePageLayout';
import DungeonDisplay from '../components/game/DungeonDisplay';
import ActionButtons from '../components/game/ActionButtons';
import LevelUpMenu from '../components/game/LevelUpMenu';
import SaveLoadMenu from '../components/game/SaveLoadMenu';
import EquipmentMenu from '../components/town/EquipmentMenu';
import { DUNGEONS } from '../data/dungeons';

const GamePage = () => {
  const { state, changeTownLocation } = useGame();
  const [showSaveMenu, setShowSaveMenu] = useState(false);
  const navigate = useNavigate();
  const location = useLocation();

  // Redirect to home if character isn't created yet
  useEffect(() => {
    if (!state.hasCreatedCharacter) {
      navigate('/');
    }
  }, [state.hasCreatedCharacter, navigate]);

  // Redirect to game-over page if isGameOver is true
  useEffect(() => {
    if (state.isGameOver) {
      navigate('/game-over');
    }
  }, [state.isGameOver, navigate]);

  // If redirecting, don't render the page content
  if (!state.hasCreatedCharacter) {
    return null;
  }

  // ダンジョン情報
  const currentDungeon = DUNGEONS[state.dungeon.type] || {
    name: 'ダンジョン'
  };

  // Handle return to town
  const handleReturnToTown = () => {
    changeTownLocation('square');
    navigate('/town');
  };

  // Handle quest navigation
  const handleQuestsNavigation = () => {
    sessionStorage.setItem('questReturnPath', location.pathname);
    navigate('/quests');
  };

  // Menu button
  const actionButtons = (
      <>
        <button
            onClick={() => handleReturnToTown()}
            className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded transition-colors min-w-[90px] md:min-w-[120px]"
        >
          街へ帰還
        </button>
        <button
            onClick={() => handleQuestsNavigation()}
            className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded transition-colors min-w-[90px] md:min-w-[120px]"
        >
          クエスト
        </button>
        <button
            onClick={() => setShowSaveMenu(true)}
            className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded transition-colors min-w-[90px] md:min-w-[120px]"
        >
          メニュー
        </button>
      </>
  );

  // Sidebar content
  const sidebarContent = (
      <EquipmentMenu/>
  );

  return (
      <>
        <ResponsivePageLayout
            title={`${currentDungeon.name} - 階層: ${state.dungeon.level}`}
            actions={actionButtons}
            sidebar={sidebarContent}
        >
          {state.isLevelingUp ? (
              <LevelUpMenu />
          ) : (
              <>
                <DungeonDisplay />
                <ActionButtons />
              </>
          )}
        </ResponsivePageLayout>

        {/* セーブ/ロードメニュー */}
        {showSaveMenu && (
            <SaveLoadMenu onClose={() => setShowSaveMenu(false)} />
        )}
      </>
  );
};

export default GamePage;
