// src/context/actions.js
// アクションタイプ定義
export const ACTION_TYPES = {
  // 既存のアクション
  ADD_LOG: 'ADD_LOG',
  UPDATE_CHARACTER: 'UPDATE_CHARACTER',
  UPDATE_DUNGEON: 'UPDATE_DUNGEON',
  RESET_GAME: 'RESET_GAME',
  GAME_OVER: 'GAME_OVER',
  START_LEVEL_UP: 'START_LEVEL_UP',
  FINISH_LEVEL_UP: 'FINISH_LEVEL_UP',
  INCREASE_STAT: 'INCREASE_STAT',
  INCREASE_SKILL: 'INCREASE_SKILL',
  SET_SPECIAL_EVENT: 'SET_SPECIAL_EVENT',
  CLEAR_SPECIAL_EVENT: 'CLEAR_SPECIAL_EVENT',
  APPLY_EVENT_EFFECT: 'APPLY_EVENT_EFFECT',
  LOAD_SAVED_GAME: 'LOAD_SAVED_GAME',
  
  // 街関連アクション
  CHANGE_TOWN_LOCATION: 'CHANGE_TOWN_LOCATION',
  UPDATE_SHOP_INVENTORY: 'UPDATE_SHOP_INVENTORY',
  RESET_SHOP_INVENTORY: 'RESET_SHOP_INVENTORY',
  ADD_RUMOR: 'ADD_RUMOR',
  UPDATE_CONVERSATION: 'UPDATE_CONVERSATION',
  PURCHASE_ITEM: 'PURCHASE_ITEM',
  UPDATE_TOWN_STATE: 'UPDATE_TOWN_STATE',
  
  // 装備関連アクション
  EQUIP_ITEM: 'EQUIP_ITEM',
  UNEQUIP_ITEM: 'UNEQUIP_ITEM',
  BUY_ITEM: 'BUY_ITEM',
  SELL_ITEM: 'SELL_ITEM',
  ADD_TO_INVENTORY: 'ADD_TO_INVENTORY',
  REMOVE_FROM_INVENTORY: 'REMOVE_FROM_INVENTORY',
  
  // クエスト関連アクション
  SET_INITIAL_QUESTS: 'SET_INITIAL_QUESTS',
  START_QUEST: 'START_QUEST',
  COMPLETE_QUEST: 'COMPLETE_QUEST',
  FAIL_QUEST: 'FAIL_QUEST',
  TRACK_QUEST: 'TRACK_QUEST',
  UPDATE_QUEST_OBJECTIVE: 'UPDATE_QUEST_OBJECTIVE',
  UPDATE_QUEST_OBJECTIVE_COUNT: 'UPDATE_QUEST_OBJECTIVE_COUNT',

  // UI関連アクション
  CHANGE_SCREEN: 'CHANGE_SCREEN',
  OPEN_DIALOG: 'OPEN_DIALOG',
  CLOSE_DIALOG: 'CLOSE_DIALOG',
  OPEN_MODAL: 'OPEN_MODAL',
  CLOSE_MODAL: 'CLOSE_MODAL'
};

// アクションクリエーター関数

// ログ関連
export const log = (text, className = '') => ({
  type: ACTION_TYPES.ADD_LOG,
  payload: { text, className }
});

// キャラクター関連
export const updateCharacter = (updates) => ({
  type: ACTION_TYPES.UPDATE_CHARACTER,
  payload: updates
});

// ダンジョン関連
export const updateDungeon = (updates) => ({
  type: ACTION_TYPES.UPDATE_DUNGEON,
  payload: updates
});

// ゲーム状態関連
export const gameOver = () => ({
  type: ACTION_TYPES.GAME_OVER
});

/**
 * ゲーム状態をリセット
 * @param {Object} options - リセットオプション
 * @param {boolean} options.hasCreatedCharacter - キャラクターが作成済みかどうか
 * @param {boolean} options.preserveInitialQuests - 初期クエストを保持するかどうか
 * @returns {Object} リセットアクション
 */
export const resetGame = (options = {}) => ({
  type: ACTION_TYPES.RESET_GAME,
  payload: {
    hasCreatedCharacter: options.hasCreatedCharacter || false,
    preserveInitialQuests: options.preserveInitialQuests || true // デフォルトで初期クエストを保持
  }
});

// レベルアップ関連
export const startLevelUp = () => ({
  type: ACTION_TYPES.START_LEVEL_UP
});

export const finishLevelUp = () => ({
  type: ACTION_TYPES.FINISH_LEVEL_UP
});

export const increaseStat = (statName) => ({
  type: ACTION_TYPES.INCREASE_STAT,
  payload: statName
});

export const increaseSkill = (skillName) => ({
  type: ACTION_TYPES.INCREASE_SKILL,
  payload: skillName
});

// イベント関連
export const setSpecialEvent = (event) => ({
  type: ACTION_TYPES.SET_SPECIAL_EVENT,
  payload: event
});

export const clearSpecialEvent = () => ({
  type: ACTION_TYPES.CLEAR_SPECIAL_EVENT
});

export const applyEventEffect = (effect) => ({
  type: ACTION_TYPES.APPLY_EVENT_EFFECT,
  payload: effect
});

// セーブ関連
export const loadSavedGame = (savedState) => ({
  type: ACTION_TYPES.LOAD_SAVED_GAME,
  payload: savedState
});

// 街関連
export const changeTownLocation = (location) => ({
  type: ACTION_TYPES.CHANGE_TOWN_LOCATION,
  payload: location
});

export const updateShopInventory = (inventoryUpdates) => ({
  type: ACTION_TYPES.UPDATE_SHOP_INVENTORY,
  payload: inventoryUpdates
});

export const resetShopInventory = () => ({
  type: ACTION_TYPES.RESET_SHOP_INVENTORY
});

export const addRumor = (rumor) => ({
  type: ACTION_TYPES.ADD_RUMOR,
  payload: rumor
});

export const updateConversation = (character, level) => ({
  type: ACTION_TYPES.UPDATE_CONVERSATION,
  payload: { character, level }
});

export const purchaseItem = (itemId, itemType) => ({
  type: ACTION_TYPES.PURCHASE_ITEM,
  payload: { itemId, itemType }
});

export const updateTownState = (updates) => ({
  type: ACTION_TYPES.UPDATE_TOWN_STATE,
  payload: updates
});

// 装備関連
export const equipItem = (slot, item) => ({
  type: ACTION_TYPES.EQUIP_ITEM,
  payload: { slot, item }
});

export const unequipItem = (slot) => ({
  type: ACTION_TYPES.UNEQUIP_ITEM,
  payload: slot
});

export const buyItem = (item) => ({
  type: ACTION_TYPES.BUY_ITEM,
  payload: item
});

export const sellItem = (item) => ({
  type: ACTION_TYPES.SELL_ITEM,
  payload: item
});

export const addToInventory = (item) => ({
  type: ACTION_TYPES.ADD_TO_INVENTORY,
  payload: item
});

export const removeFromInventory = (itemId) => ({
  type: ACTION_TYPES.REMOVE_FROM_INVENTORY,
  payload: itemId
});

// クエスト関連

// 初期クエストを設定
export const setInitialQuests = (quests) => ({
  type: ACTION_TYPES.SET_INITIAL_QUESTS,
  payload: quests
});

// クエストを開始
export const startQuest = (questId) => ({
  type: ACTION_TYPES.START_QUEST,
  payload: questId
});

// クエストを完了
export const completeQuest = (questId) => ({
  type: ACTION_TYPES.COMPLETE_QUEST,
  payload: questId
});

// クエストを失敗
export const failQuest = (questId) => ({
  type: ACTION_TYPES.FAIL_QUEST,
  payload: questId
});

// クエストを追跡
export const trackQuest = (questId) => ({
  type: ACTION_TYPES.TRACK_QUEST,
  payload: questId
});

// UI関連
export const changeScreen = (screenName) => ({
  type: ACTION_TYPES.CHANGE_SCREEN,
  payload: screenName
});

export const openDialog = (content) => ({
  type: ACTION_TYPES.OPEN_DIALOG,
  payload: content
});

export const closeDialog = () => ({
  type: ACTION_TYPES.CLOSE_DIALOG
});

export const openModal = (content) => ({
  type: ACTION_TYPES.OPEN_MODAL,
  payload: content
});

export const closeModal = () => ({
  type: ACTION_TYPES.CLOSE_MODAL
});
