// src/components/quests/QuestNotification.js
import React, { useState, useEffect } from 'react';
import { useGame } from '../../context/GameContext';
import { QUEST_DATABASE } from '../../data/quests';

/**
 * クエスト達成通知コンポーネント
 * 目標達成時や完了時に表示する通知
 */
const QuestNotification = () => {
  const { state } = useGame();
  const [notification, setNotification] = useState(null);
  const [visible, setVisible] = useState(false);
  
  // questJournalの変更を監視して通知を表示
  useEffect(() => {
    if (!state.questJournal) return;
    
    // 進行中のクエストとトラックされているクエスト
    const activeQuests = state.questJournal.activeQuests || [];
    const trackedQuestId = state.questJournal.trackedQuestId;
    
    // トラックされているクエストを優先的に見る
    let targetQuest = null;
    if (trackedQuestId) {
      targetQuest = activeQuests.find(q => q.id === trackedQuestId);
    }
    
    // トラックされているクエストがなければ、最初の進行中クエストを使用
    if (!targetQuest && activeQuests.length > 0) {
      targetQuest = activeQuests[0];
    }
    
    if (targetQuest) {
      // クエストデータを取得
      const questData = QUEST_DATABASE.find(q => q.id === targetQuest.id);
      if (!questData) return;
      
      // 全ての目標が完了したかどうか
      const allCompleted = targetQuest.objectives.every(obj => obj.isCompleted);
      if (allCompleted) {
        // クエスト完了の通知
        setNotification({
          type: 'complete',
          message: `クエスト「${questData.title}」の全ての目標を達成しました！`,
          questTitle: questData.title
        });
        setVisible(true);
      } else {
        // 目標達成の進行度を確認
        const completedCount = targetQuest.objectives.filter(obj => obj.isCompleted).length;
        const totalCount = targetQuest.objectives.length;
        
        if (completedCount > 0) {
          setNotification({
            type: 'progress',
            message: `クエスト「${questData.title}」進行: ${completedCount}/${totalCount}の目標を達成`,
            questTitle: questData.title,
            progress: Math.floor((completedCount / totalCount) * 100)
          });
          setVisible(true);
        }
      }
    }
  }, [state.questJournal]);
  
  // 通知を表示して自動的に消す
  useEffect(() => {
    if (visible) {
      const timer = setTimeout(() => {
        setVisible(false);
      }, 5000);
      
      return () => clearTimeout(timer);
    }
  }, [visible]);
  
  // 表示されてなければ何も表示しない
  if (!visible || !notification) {
    return null;
  }
  
  return (
    <div className={`fixed bottom-16 right-6 p-4 rounded-lg shadow-lg transition-opacity duration-300 ${
      notification.type === 'complete' 
        ? 'bg-green-700 text-white' 
        : 'bg-blue-700 text-white'
    } max-w-xs sm:max-w-sm`}>
      <div className="flex items-start space-x-3">
        <div className={`w-3 h-3 rounded-full mt-1 flex-shrink-0 ${
          notification.type === 'complete' 
            ? 'bg-green-300' 
            : 'bg-blue-300'
        }`}></div>
        <div className="flex-1">
          <p className="font-medium">{notification.message}</p>
          {notification.type === 'progress' && (
            <div className="mt-3">
              <div className="w-full h-2 bg-blue-900 rounded-full overflow-hidden">
                <div 
                  className="h-full bg-blue-300 transition-all duration-300"
                  style={{ width: `${notification.progress}%` }}
                ></div>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default QuestNotification;
