// src/components/town/WeaponShop.js
import React, { useState, useEffect } from 'react';
import { useGame } from '../../context/GameContext';
import ConversationPanel from './ConversationPanel';

const WeaponShop = ({ onBack }) => {
  const { state, generateShopInventory, buyItem } = useGame();
  const [showWeapons, setShowWeapons] = useState(false);
  const [showTalk, setShowTalk] = useState(false);
  const [selectedWeapon, setSelectedWeapon] = useState(null);
  
  // 初回表示時に在庫を生成
  useEffect(() => {
    if (state.town.shopInventory.weapons.length === 0) {
      generateShopInventory('weapons', state.character.level);
    }
  }, []);
  
  // 店の在庫
  const weapons = state.town.shopInventory.weapons || [];
  
  // プレイヤーの所持金
  const gold = state.character.gold;
  
  return (
    <div className="bg-indigo-900 rounded-lg shadow-lg p-4">
      <h3 className="text-xl font-semibold mb-4 text-sky-300">武器屋「鋼の意志」</h3>
      
      {!showWeapons && !showTalk ? (
        <>
          <p className="mb-6">
            壁に様々な武器が飾られた店内。鍛冶場からは金属を打つ音が聞こえてくる。
            カウンターには若い女性店員が立っており、あなたに微笑みかける。
          </p>
          
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 mb-6">
            <button 
              onClick={() => setShowWeapons(true)}
              className="bg-indigo-800 hover:bg-indigo-700 rounded-lg p-4 text-left transition-colors"
            >
              <h4 className="font-semibold text-sky-300">武器を見る</h4>
              <p className="text-sm text-indigo-200">販売中の武器を確認する。</p>
            </button>
            
            <button 
              onClick={() => setShowTalk(true)}
              className="bg-indigo-800 hover:bg-indigo-700 rounded-lg p-4 text-left transition-colors"
            >
              <h4 className="font-semibold text-sky-300">店員と話す</h4>
              <p className="text-sm text-indigo-200">武器について詳しい女性店員と会話する。</p>
            </button>
          </div>
        </>
      ) : showWeapons ? (
        <div className="weapon-list">
          <h4 className="font-semibold text-lg mb-4 text-amber-300">販売中の武器</h4>
          
          {selectedWeapon ? (
            <div className="weapon-detail mb-6">
              <div className="bg-indigo-800 p-4 rounded-lg mb-4">
                <h5 className="font-medium text-sky-200 mb-2">{selectedWeapon.name}</h5>
                <div className="grid grid-cols-2 gap-2 mb-3">
                  <div className="text-sm">攻撃力: <span className="text-green-300">+{selectedWeapon.attack}</span></div>
                  <div className="text-sm">重さ: <span className="text-yellow-300">{selectedWeapon.weight}</span></div>
                </div>
                <p className="text-indigo-200 text-sm mb-3">{selectedWeapon.description}</p>
                <div className="flex justify-between items-center">
                  <span className="text-amber-300">{selectedWeapon.price} G</span>
                  <button
                    onClick={() => buyItem(selectedWeapon)}
                    disabled={gold < selectedWeapon.price}
                    className={`py-1 px-3 rounded ${
                      gold >= selectedWeapon.price
                        ? 'bg-green-600 hover:bg-green-700 text-white'
                        : 'bg-gray-600 text-gray-300 cursor-not-allowed'
                    }`}
                  >
                    購入する
                  </button>
                </div>
              </div>
              
              <button
                onClick={() => setSelectedWeapon(null)}
                className="bg-indigo-700 hover:bg-indigo-600 text-white py-1 px-3 rounded-lg transition-colors"
              >
                他の武器を見る
              </button>
            </div>
          ) : (
            <>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-6">
                {weapons.map(weapon => (
                  <div
                    key={weapon.id}
                    onClick={() => setSelectedWeapon(weapon)}
                    className="bg-indigo-800 hover:bg-indigo-700 p-3 rounded-lg cursor-pointer transition-colors"
                  >
                    <div className="flex justify-between items-center mb-1">
                      <h5 className="font-medium text-sky-200">{weapon.name}</h5>
                      <span className="text-amber-300">{weapon.price} G</span>
                    </div>
                    <div className="text-xs text-indigo-300">攻撃力: +{weapon.attack}</div>
                  </div>
                ))}
              </div>
              
              <div className="mb-4 bg-indigo-800 p-2 rounded">
                <span className="text-sm">所持金: <span className="text-amber-300">{gold} G</span></span>
              </div>
            </>
          )}
          
          <button
            onClick={() => setShowWeapons(false)}
            className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded-lg transition-colors"
          >
            戻る
          </button>
        </div>
      ) : showTalk ? (
        <ConversationPanel
          characterId="weaponShopGirl"
          onExitConversation={() => setShowTalk(false)}
        />
      ) : null}
      
      {/* 戻るボタン */}
      {!showWeapons && !showTalk && (
        <button
          onClick={onBack}
          className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded-lg transition-colors"
        >
          広場へ戻る
        </button>
      )}
    </div>
  );
};

export default WeaponShop;
