// src/context/GameContext.js
import { createContext, useContext, useReducer, useEffect, useRef } from 'react';
import initialGameState from './initialState';
import gameReducer from './reducers';
import { ACTION_TYPES, setInitialQuests } from './actions';
import createGameUtils from './utils';
import { QUEST_DATABASE } from '../data/quests';

// Create game context
const GameContext = createContext();

// Context provider
export const GameProvider = ({ children }) => {
  const [state, dispatch] = useReducer(gameReducer, initialGameState);
  
  // 初回レンダリング時のみメッセージを表示するために
  // useRefを使用して初期化済みかどうかを追跡
  const isInitialized = useRef(false);

  useEffect(() => {
    if (!isInitialized.current) {
      console.log("GameProvider - Initializing game...");
      
      // 前提条件のないクエストを取得して初期化
      const initialQuests = QUEST_DATABASE.filter(quest => 
        !quest.requiredQuestIds || quest.requiredQuestIds.length === 0
      );
      
      console.log("Initial quests to set:", initialQuests);
      
      // 初期クエストを設定
      if (initialQuests && initialQuests.length > 0) {
        dispatch(setInitialQuests(initialQuests));
      }
      
      // ログを追加
      dispatch({ 
        type: ACTION_TYPES.ADD_LOG, 
        payload: { 
          text: 'ゲーム開始！ダンジョンの探索を始めましょう。', 
          className: '' 
        } 
      });
      
      isInitialized.current = true;
    }
  }, []);
  
  // Create game utility functions
  const gameUtils = createGameUtils(state, dispatch);
  
  return (
    <GameContext.Provider value={{ state, ...gameUtils }}>
      {children}
    </GameContext.Provider>
  );
}

// Custom hook to use the game context
export function useGame() {
  const context = useContext(GameContext);
  if (!context) {
    throw new Error('useGame must be used within a GameProvider');
  }
  return context;
}
