// src/components/game/TrapDisplay.js
import { useState } from 'react';
import { useGame } from '../../context/GameContext';

const TrapDisplay = ({ trap, onTrapResolved }) => {
  const { state, updateCharacter, log, checkRoll, gameOver } = useGame();
  const { character } = state;
  
  const [isResolving, setIsResolving] = useState(false);
  const [result, setResult] = useState(null);
  
  // トラップを解除する試み
  const handleDisarmTrap = () => {
    setIsResolving(true);
    
    setTimeout(() => {
      // トラップ解除の判定
      const disarmResult = checkRoll(
        character.stats.dex,
        character.skills.trap,
        trap.difficulty + 2 // 解除は回避より難しい
      );
      
      if (disarmResult.success) {
        log(`トラップ解除判定成功！${trap.name}を無効化しました。(${disarmResult.roll}を出して合計${disarmResult.total})`, 'success');
        setResult({ success: true, disarmed: true });
        
        // 解除成功でボーナスXP
        if (onTrapResolved) {
          onTrapResolved('disarmed');
        }
      } else {
        // 解除失敗 - トラップが作動
        log(`トラップ解除判定失敗！${trap.name}が作動した！(${disarmResult.roll}を出して合計${disarmResult.total})`, 'failure');
        handleTrapEffect();
      }
      
      setIsResolving(false);
    }, 500);
  };
  
  // トラップを回避する試み
  const handleEvadeTrap = () => {
    setIsResolving(true);
    
    setTimeout(() => {
      // トラップ回避の判定
      const evadeResult = checkRoll(
        character.stats.dex,
        character.skills.trap,
        trap.difficulty
      );
      
      if (evadeResult.success) {
        log(`トラップ回避判定成功！${trap.name}を回避しました。(${evadeResult.roll}を出して合計${evadeResult.total})`, 'success');
        setResult({ success: true, disarmed: false });
        
        if (onTrapResolved) {
          onTrapResolved('evaded');
        }
      } else {
        // 回避失敗 - トラップが作動
        log(`トラップ回避判定失敗！${trap.name}が作動した！(${evadeResult.roll}を出して合計${evadeResult.total})`, 'failure');
        handleTrapEffect();
      }
      
      setIsResolving(false);
    }, 500);
  };
  
  // トラップの効果を適用
  const handleTrapEffect = () => {
    const damage = trap.damage;
    const newHp = Math.max(0, character.hp - damage);
    
    updateCharacter({ hp: newHp });
    
    log(`${trap.name}により${damage}ダメージを受けました！`, 'failure');
    
    // Check for game over
    if (newHp <= 0) {
      gameOver();
      return;
    }
    
    setResult({ success: false, damage });
    
    if (onTrapResolved) {
      onTrapResolved('triggered');
    }
  };
  
  // 表示内容を決定
  const renderContent = () => {
    if (result) {
      // トラップ判定後の表示
      if (result.success) {
        return (
          <div className="text-green-300">
            <p className="mb-4">
              {result.disarmed 
                ? `${trap.name}を無効化しました！`
                : `${trap.name}を回避しました！`}
            </p>
            <button
              onClick={() => onTrapResolved('continue')}
              className="bg-green-600 hover:bg-green-700 text-white font-medium py-2 px-6 rounded-lg transition-colors"
            >
              先に進む
            </button>
          </div>
        );
      } else {
        return (
          <div className="text-red-300">
            <p className="mb-4">
              {`${trap.name}により${result.damage}ダメージを受けました！`}
            </p>
            <button
              onClick={() => onTrapResolved('continue')}
              className="bg-indigo-600 hover:bg-indigo-700 text-white font-medium py-2 px-6 rounded-lg transition-colors"
            >
              先に進む
            </button>
          </div>
        );
      }
    } else {
      // 初期表示 - トラップ発見時
      return (
        <div>
          <p className="mb-4">
            どうしますか？
          </p>
          <div className="flex flex-col md:flex-row space-y-2 md:space-y-0 md:space-x-4 justify-center">
            <button
              onClick={handleDisarmTrap}
              disabled={isResolving}
              className={`bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-lg transition-colors ${
                isResolving ? 'opacity-50 cursor-not-allowed' : ''
              }`}
            >
              トラップを解除する
            </button>
            <button
              onClick={handleEvadeTrap}
              disabled={isResolving}
              className={`bg-purple-600 hover:bg-purple-700 text-white font-medium py-2 px-4 rounded-lg transition-colors ${
                isResolving ? 'opacity-50 cursor-not-allowed' : ''
              }`}
            >
              トラップを回避する
            </button>
          </div>
        </div>
      );
    }
  };
  
  return (
    <div className="bg-red-900/40 rounded-lg p-4 text-center py-6">
      <h3 className="text-xl font-bold text-red-300 mb-4">
        {trap.name}を発見！
      </h3>
      
      <p className="mb-4 text-red-100">
        {trap.description || "危険なトラップが仕掛けられています。"}
      </p>
      
      {renderContent()}
    </div>
  );
};

export default TrapDisplay;
