// src/context/reducers/uiReducer.js
import { ACTION_TYPES } from '../actions';

export const uiReducer = (state, action) => {
  switch (action.type) {
    case ACTION_TYPES.GAME_OVER:
      return {
        ...state,
        isGameOver: true
      };
      
    case ACTION_TYPES.START_LEVEL_UP:
      return {
        ...state,
        isLevelingUp: true
      };
      
    case ACTION_TYPES.FINISH_LEVEL_UP:
      return {
        ...state,
        isLevelingUp: false
      };
      
    case ACTION_TYPES.UPDATE_CHARACTER:
      return {
        ...state,
        hasCreatedCharacter: true
      };
      
    case ACTION_TYPES.CHANGE_SCREEN:
      return {
        ...state,
        ui: {
          ...state.ui,
          currentScreen: action.payload
        }
      };
      
    case ACTION_TYPES.OPEN_DIALOG:
      return {
        ...state,
        ui: {
          ...state.ui,
          dialogOpen: true,
          dialogContent: action.payload
        }
      };
      
    case ACTION_TYPES.CLOSE_DIALOG:
      return {
        ...state,
        ui: {
          ...state.ui,
          dialogOpen: false,
          dialogContent: null
        }
      };
      
    case ACTION_TYPES.OPEN_MODAL:
      return {
        ...state,
        ui: {
          ...state.ui,
          modalOpen: true,
          modalContent: action.payload
        }
      };
      
    case ACTION_TYPES.CLOSE_MODAL:
      return {
        ...state,
        ui: {
          ...state.ui,
          modalOpen: false,
          modalContent: null
        }
      };
      
    default:
      return state;
  }
};

export default uiReducer;
