// src/components/quests/QuestCard.js
import React, { useState } from 'react';
import { findItemById } from '../../data/itemDatabase';
import { getCharacterInfo } from '../../data/characters';
import QuestRewardDialog from './QuestRewardDialog';

const QuestCard = ({ quest, questData, isTracked, onTrack }) => {
  const [expanded, setExpanded] = useState(false);
  const [showRewardDialog, setShowRewardDialog] = useState(false);
  
  // 目標の進捗度を計算
  const completedObjectives = quest.objectives.filter(obj => obj.isCompleted).length;
  const totalObjectives = quest.objectives.length;
  const progressPercent = Math.round((completedObjectives / totalObjectives) * 100);
  
  // クエスト完了条件を満たしているか確認
  const canComplete = completedObjectives === totalObjectives;
  
  // クエスト完了ハンドラ - 報酬ダイアログを表示
  const handleComplete = () => {
    if (canComplete) {
      setShowRewardDialog(true);
    }
  };

  // 報酬ダイアログを閉じる
  const handleCloseRewardDialog = () => {
    setShowRewardDialog(false);
  };
  
  // 詳細表示切り替え
  const toggleExpanded = () => {
    setExpanded(!expanded);
  };
  
  return (
    <>
      <div className={`rounded-lg overflow-hidden mb-4 ${
        isTracked ? 'bg-indigo-600' : 'bg-indigo-800'
      }`}>
        {/* クエストのヘッダー部分 - クリックで展開/折りたたみ */}
        <div 
          className={`px-4 py-3 flex justify-between items-center cursor-pointer ${
            isTracked ? 'bg-indigo-500' : 'bg-indigo-700'
          }`}
          onClick={toggleExpanded}
        >
          <div>
            <h3 className="font-semibold text-sky-300">{questData.title}</h3>
            <div className="text-xs text-indigo-300">
              {questData.type === 'tutorial' ? 'チュートリアル' : questData.type === 'combat' ? '戦闘' : '探索'}
              {questData.difficulty && <span className="ml-2">難易度: {questData.difficulty}</span>}
            </div>
          </div>
          <div className="flex items-center space-x-2">
            <div className="text-xs bg-indigo-700 px-2 py-1 rounded-full">
              {progressPercent}%
            </div>
            {quest.timeLimit && (
              <div className="text-xs bg-amber-700 px-2 py-1 rounded-full">
                残り: {quest.timeLimit}
              </div>
            )}
            <div className="text-xs">
              {expanded ? '▲' : '▼'}
            </div>
          </div>
        </div>
        
        {/* 展開時に表示される詳細情報 */}
        {expanded && (
          <div className={`px-4 py-3 ${
            isTracked ? 'bg-indigo-700' : 'bg-indigo-900'
          }`}>
            {/* 依頼者情報 */}
            {questData.giver && (
              <div className="mb-2">
                <h4 className="text-sm font-medium mb-1 text-sky-200">依頼者:</h4>
                <div className="text-sm text-indigo-100">
                  <span>{getCharacterInfo(questData.giver)}</span>
                </div>
              </div>
            )}

            <p className="text-sm mb-2 text-indigo-100">
              {questData.description}
            </p>
            
            {/* 目標リスト */}
            <div className="mb-2">
              <h4 className="text-sm font-medium mb-2 text-sky-200">目標:</h4>
              <ul className="space-y-2">
                {quest.objectives.map((objective, index) => {
                  const objectiveData = questData.objectives[index];
                  return (
                    <li 
                      key={objective.id} 
                      className={`flex items-start text-sm ${
                        objective.isCompleted ? 'text-green-300' : 'text-indigo-200'
                      }`}
                    >
                      <div className={`w-3 h-3 mt-1 mr-2 rounded-full ${objective.isCompleted ? 'bg-green-500' : 'bg-indigo-600'}`}></div>
                      <div className="flex-1">
                        <div>{objectiveData.description}</div>
                        {objective.currentCount !== undefined && (
                          <div className="text-xs mt-1">
                            進捗: {objective.currentCount}/{objective.maxCount}
                          </div>
                        )}
                        {!objective.isCompleted && objectiveData.hint && (
                          <div className="text-xs mt-1 text-indigo-400">
                            ヒント: {objectiveData.hint}
                          </div>
                        )}
                      </div>
                    </li>
                  );
                })}
              </ul>
            </div>
            
            {/* 報酬情報 */}
            <div className="mb-2">
              <h4 className="text-sm font-medium mb-2 text-sky-200">報酬:</h4>
              {questData.rewards && questData.rewards.length > 0 ? (
                <ul className="text-sm space-y-1">
                  {questData.rewards.map((reward, index) => (
                    <li key={index} className="text-amber-300">
                      {reward.type === 'gold' && `${reward.amount} ゴールド`}
                      {reward.type === 'xp' && `${reward.amount} 経験値`}
                      {reward.type === 'item' && `アイテム: ${findItemById(reward.id)?.name || reward.id}`}
                    </li>
                  ))}
                </ul>
              ) : (
                <p className="text-sm text-indigo-300">なし</p>
              )}
            </div>
            
            {/* アクションボタン */}
            <div className="flex justify-between pt-2">
              <button
                onClick={(e) => {
                  e.stopPropagation();
                  onTrack();
                }}
                className={`text-xs px-3 py-1 rounded ${
                  isTracked 
                    ? 'bg-indigo-500 hover:bg-indigo-400 text-white' 
                    : 'bg-blue-600 hover:bg-blue-700 text-white'
                }`}
              >
                {isTracked ? '追跡解除' : '追跡する'}
              </button>
              
              {canComplete && (
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    handleComplete();
                  }}
                  className="text-xs px-3 py-1 rounded bg-green-600 hover:bg-green-700 text-white"
                >
                  完了報告
                </button>
              )}
            </div>
          </div>
        )}
      </div>
      
      {/* 報酬ダイアログ */}
      {showRewardDialog && (
        <QuestRewardDialog 
          questId={quest.id} 
          onClose={handleCloseRewardDialog} 
        />
      )}
    </>
  );
};

export default QuestCard;
