// src/components/town/EquipmentMenu.js
import { useState } from 'react';
import { useGame } from '../../context/GameContext';

const EquipmentMenu = () => {
  const { state, equipItem, unequipItem, calculateAttackPower, calculateDefensePower, calculateEvasion } = useGame();
  const [showInventory, setShowInventory] = useState(false);
  
  const { character } = state;
  const { equipment, inventory } = character;
  
  // 攻撃力、防御力、回避力を計算
  const attackPower = calculateAttackPower();
  const defensePower = calculateDefensePower();
  const evasion = calculateEvasion();
  
  return (
    <div className="bg-indigo-900 rounded-lg shadow-lg overflow-hidden mt-4">
      <h3 className="text-lg font-semibold bg-indigo-800 py-2 px-4 text-sky-300">
        装備
      </h3>
      
      <div className="p-4">
        {/* 現在の装備表示 */}
        <div className="mb-4">
          <div className="grid grid-cols-2 gap-4">
            <div className="bg-indigo-800 p-3 rounded-lg">
              <h4 className="text-sm font-medium mb-2 text-sky-200">武器</h4>
              {equipment.weapon ? (
                <div>
                  <div className="flex justify-between items-center mb-1">
                    <span className="font-medium text-white">{equipment.weapon.name}</span>
                    <button
                      onClick={() => unequipItem('weapon')}
                      className="text-xs bg-indigo-700 hover:bg-indigo-600 text-white px-2 py-1 rounded"
                    >
                      外す
                    </button>
                  </div>
                  <div className="text-xs text-indigo-300">攻撃力: +{equipment.weapon.attack}</div>
                </div>
              ) : (
                <div className="text-indigo-400 text-sm">装備なし</div>
              )}
            </div>
            
            <div className="bg-indigo-800 p-3 rounded-lg">
              <h4 className="text-sm font-medium mb-2 text-sky-200">防具</h4>
              {equipment.armour ? (
                <div>
                  <div className="flex justify-between items-center mb-1">
                    <span className="font-medium text-white">{equipment.armour.name}</span>
                    <button
                      onClick={() => unequipItem('armour')}
                      className="text-xs bg-indigo-700 hover:bg-indigo-600 text-white px-2 py-1 rounded"
                    >
                      外す
                    </button>
                  </div>
                  <div className="text-xs text-indigo-300">防御力: +{equipment.armour.defense}</div>
                </div>
              ) : (
                <div className="text-indigo-400 text-sm">装備なし</div>
              )}
            </div>
          </div>
        </div>
        
        {/* 戦闘能力概要 */}
        <div className="mb-4 p-3 bg-indigo-800 rounded-lg">
          <h4 className="text-sm font-medium mb-2 text-sky-200">戦闘能力</h4>
          <div className="grid grid-cols-3 gap-2 text-sm">
            <div>
              <span className="text-indigo-300">攻撃力:</span>
              <span className="ml-2 text-white">{attackPower}</span>
            </div>
            <div>
              <span className="text-indigo-300">防御力:</span>
              <span className="ml-2 text-white">{defensePower}</span>
            </div>
            <div>
              <span className="text-indigo-300">回避力:</span>
              <span className="ml-2 text-white">{evasion}</span>
            </div>
          </div>
        </div>
        
        {/* インベントリボタン */}
        <button
          onClick={() => setShowInventory(!showInventory)}
          className="w-full py-2 px-4 bg-indigo-700 hover:bg-indigo-600 text-white rounded-lg transition-colors"
        >
          {showInventory ? "インベントリを閉じる" : "インベントリを見る"}
        </button>
        
        {/* インベントリ表示 */}
        {showInventory && (
          <div className="mt-4">
            <h4 className="text-sm font-medium mb-2 text-sky-200">インベントリ</h4>
            
            {inventory.length > 0 ? (
              <div className="space-y-2">
                {inventory.map(item => (
                  <InventoryItem 
                    key={item.id} 
                    item={item} 
                    onEquip={() => {
                      if (item.attack !== undefined) {
                        equipItem('weapon', item);
                      } else if (item.defense !== undefined) {
                        equipItem('armour', item);
                      }
                    }} 
                  />
                ))}
              </div>
            ) : (
              <div className="text-indigo-400 text-sm p-3 bg-indigo-800 rounded-lg">
                インベントリは空です
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

// インベントリアイテムコンポーネント
const InventoryItem = ({ item, onEquip }) => {
  const [showDetails, setShowDetails] = useState(false);
  
  // アイテムの種類を判定
  const itemType = item.attack !== undefined ? 'weapon' : 
                  item.defense !== undefined ? 'armour' : 'accessory';
  
  return (
    <div className="bg-indigo-800 rounded-lg overflow-hidden">
      <div 
        className="p-3 cursor-pointer flex justify-between items-center"
        onClick={() => setShowDetails(!showDetails)}
      >
        <div>
          <span className="font-medium text-white">{item.name}</span>
          <div className="text-xs text-indigo-300">
            {itemType === 'weapon' ? `攻撃力: +${item.attack}` : 
             itemType === 'armour' ? `防御力: +${item.defense}` : 
             '装飾品'}
          </div>
        </div>
        <div className="flex space-x-2">
          {(itemType === 'weapon' || itemType === 'armour') && (
            <button
              onClick={(e) => {
                e.stopPropagation();
                onEquip();
              }}
              className="text-xs bg-green-600 hover:bg-green-700 text-white px-2 py-1 rounded"
            >
              装備
            </button>
          )}
          <span className="text-xs bg-indigo-700 px-2 py-1 rounded">
            {showDetails ? '▲' : '▼'}
          </span>
        </div>
      </div>
      
      {showDetails && (
        <div className="p-3 pt-0 border-t border-indigo-700">
          <p className="text-sm text-indigo-200 mb-2">{item.description || '説明なし'}</p>
          
          <div className="grid grid-cols-2 gap-2 text-xs">
            {itemType === 'weapon' && (
              <>
                <div>
                  <span className="text-indigo-300">攻撃力:</span>
                  <span className="ml-1 text-green-300">+{item.attack}</span>
                </div>
                <div>
                  <span className="text-indigo-300">重さ:</span>
                  <span className="ml-1 text-yellow-300">{item.weight}</span>
                </div>
              </>
            )}
            
            {itemType === 'armour' && (
              <>
                <div>
                  <span className="text-indigo-300">防御力:</span>
                  <span className="ml-1 text-green-300">+{item.defense}</span>
                </div>
                <div>
                  <span className="text-indigo-300">重さ:</span>
                  <span className="ml-1 text-yellow-300">{item.weight}</span>
                </div>
              </>
            )}
            
            {item.price > 0 && (
              <div className="col-span-2">
                <span className="text-indigo-300">価値:</span>
                <span className="ml-1 text-amber-300">{item.price} G</span>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default EquipmentMenu;
