// src/pages/HomePage.js
import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useGame } from '../context/GameContext';
import CharacterCreator from '../components/game/CharacterCreator';
import { getSaveDateTime, getSavedGameContext } from '../utils/SaveLoadSystem';

const HomePage = () => {
  const { resetGame, hasSavedGame, loadSavedGame, state } = useGame();
  const [showCharacterCreator, setShowCharacterCreator] = useState(false);
  const [saveExists, setSaveExists] = useState(false);
  const [saveDate, setSaveDate] = useState(null);
  const [saveContext, setSaveContext] = useState(null);
  const navigate = useNavigate();

  // セーブデータの存在を確認
  useEffect(() => {
    const exists = hasSavedGame();
    setSaveExists(exists);
    
    if (exists) {
      const savedAt = getSaveDateTime();
      if (savedAt) {
        const date = new Date(savedAt);
        setSaveDate(date.toLocaleString());
      }
      
      // セーブデータのコンテキストを取得
      const context = getSavedGameContext();
      setSaveContext(context);
    }
  }, [hasSavedGame]);

  // 新しいゲームを開始
  const handleStartGame = () => {
    console.log("Starting new game...");
    
    // resetGame はクエストを初期化することを確認
    resetGame({ preserveInitialQuests: true });
    
    // キャラクター作成画面に遷移
    setShowCharacterCreator(true);
  };
  
  // セーブデータからゲームを続行
  const handleContinueGame = () => {
    if (loadSavedGame()) {
      // 保存されたコンテキストに基づいて適切なページに遷移
      switch (saveContext) {
        case 'dungeon':
          navigate('/game');
          break;
        case 'dungeon-select':
          navigate('/dungeon-select');
          break;
        case 'town':
        default:
          navigate('/town');
          break;
      }
    }
  };

  // キャラクター作成画面を表示中の場合
  if (showCharacterCreator) {
    return <CharacterCreator />;
  }

  // セーブコンテキストを表示用に変換
  const getContextDisplay = (context) => {
    switch (context) {
      case 'dungeon':
        return 'ダンジョン探索中';
      case 'dungeon-select':
        return 'ダンジョン選択中';
      case 'town':
        return '街中';
      default:
        return context || '不明';
    }
  };

  return (
    <div className="max-w-3xl mx-auto text-center py-10">
      <h1 className="text-5xl font-bold mb-8 text-sky-400 tracking-wider">
        ダンジョンハック
      </h1>
      
      <div className="bg-indigo-900 rounded-lg shadow-lg p-8 mb-8">
        <p className="text-xl mb-6">
          古代の迷宮に挑む冒険者になって、富と名声を手に入れよう！
        </p>
        <p className="mb-8">
          モンスターを倒し、罠を回避し、宝物を見つけながらダンジョンの奥へと進みましょう。あなたの冒険がここから始まります。
        </p>
        
        <div className="space-y-4">
          {saveExists && (
            <div className="mb-6">
              <div className="p-4 bg-indigo-800 rounded mb-3 text-left">
                <h3 className="font-semibold mb-1">セーブデータが見つかりました</h3>
                <p className="text-sm mb-1">最終保存日時: <span className="text-amber-300">{saveDate}</span></p>
                <p className="text-sm">保存場所: <span className="text-sky-300">{getContextDisplay(saveContext)}</span></p>
              </div>
              
              <button 
                onClick={handleContinueGame}
                className="w-full bg-green-600 hover:bg-green-700 text-white font-bold py-3 px-6 rounded-lg transition-colors text-lg shadow-md mb-2"
              >
                冒険を続ける
              </button>
            </div>
          )}
          
          <button 
            onClick={handleStartGame}
            className="w-full bg-indigo-600 hover:bg-indigo-700 text-white font-bold py-3 px-6 rounded-lg transition-colors text-lg shadow-md"
          >
            新しい冒険を始める
          </button>
        </div>
      </div>
      
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="bg-indigo-800 rounded-lg p-6">
          <h2 className="text-xl font-semibold mb-3 text-sky-300">ゲーム特徴</h2>
          <ul className="text-left space-y-2">
            <li>ターン制バトルシステム</li>
            <li>キャラクター成長とレベルアップ</li>
            <li>多種多様なモンスターと宝物</li>
            <li>ランダム生成ダンジョン</li>
            <li>シンプルながら奥深い戦略性</li>
          </ul>
        </div>
        
        <div className="bg-indigo-800 rounded-lg p-6">
          <h2 className="text-xl font-semibold mb-3 text-sky-300">操作方法</h2>
          <ul className="text-left space-y-2">
            <li>街で装備を整え、情報を集めよう</li>
            <li>「攻撃」「魔法」「交渉」でモンスターと戦う</li>
            <li>ダンジョンでは罠に注意！</li>
            <li>レベルアップでステータスと特技を強化</li>
            <li>ボスを倒してダンジョンを攻略しよう！</li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default HomePage;
