// src/context/utils/townUtils.js
import { ACTION_TYPES } from '../actions';
import itemDatabase from '../../data/itemDatabase';

/**
 * 街関連のユーティリティ関数を作成
 * @param {Object} state - 現在のゲームステート
 * @param {Function} dispatch - リデューサーディスパッチ関数
 * @param {Object} logUtils - ログユーティリティ関数
 * @returns {Object} 街関連ユーティリティ関数
 */
const createTownUtils = (state, dispatch, logUtils) => {
  const { log } = logUtils;
  
  // ユーティリティオブジェクトを先に宣言
  const utils = {};

  /**
   * 街の場所を変更
   * @param {string} location - 新しい場所 ('square', 'tavern', 'weaponShop', 'armourShop')
   */
  utils.changeTownLocation = (location) => {
    dispatch({
      type: ACTION_TYPES.CHANGE_TOWN_LOCATION,
      payload: location
    });
    
    let locationName = '';
    switch(location) {
      case 'square': locationName = '街の広場'; break;
      case 'tavern': locationName = '酒場「冒険者の休息」'; break;
      case 'weaponShop': locationName = '武器屋「鋼の意志」'; break;
      case 'armourShop': locationName = '防具屋「頑丈な盾」'; break;
      default: locationName = location;
    }
    
    log(`${locationName}に移動しました。`);
  };
  
  /**
   * ショップの在庫を更新
   * @param {string} shopType - ショップタイプ ('weapons', 'armours')
   * @param {Array} inventory - 新しい在庫アイテムの配列
   */
  utils.updateShopInventory = (shopType, inventory) => {
    dispatch({
      type: ACTION_TYPES.UPDATE_SHOP_INVENTORY,
      payload: { [shopType]: inventory }
    });
  };
  
  /**
   * ショップの在庫を生成
   * @param {string} shopType - ショップタイプ ('weapons', 'armours')
   * @param {number} level - プレイヤーレベル（品揃えに影響）
   */
  utils.generateShopInventory = (shopType, level) => {
    let possibleItems = [];
    let inventory = [];
    
    if (shopType === 'weapons') {
      possibleItems = itemDatabase.weapons.filter(item => item.price > 0);
    } else if (shopType === 'armours') {
      possibleItems = itemDatabase.armours.filter(item => item.price > 0);
    }
    
    // レベルに応じた品揃えを生成
    const itemCount = 3 + Math.floor(level / 3);
    const maxItemIndex = Math.min(Math.floor(level / 2), possibleItems.length - 1);
    
    for (let i = 0; i < itemCount; i++) {
      const randomIndex = Math.floor(Math.random() * (maxItemIndex + 1));
      const item = { ...possibleItems[randomIndex] };
      
      // 在庫にすでに存在するか確認
      if (!inventory.some(invItem => invItem.id === item.id)) {
        inventory.push(item);
      }
    }
    
    // 在庫を更新
    utils.updateShopInventory(shopType, inventory);
  };
  
  /**
   * うわさを追加
   * @param {Object} rumor - うわさオブジェクト
   */
  utils.addRumor = (rumor) => {
    dispatch({
      type: ACTION_TYPES.ADD_RUMOR,
      payload: rumor
    });
    
    log(`うわさ ${rumor.title}を聞きました。`, 'success');
  };
  
  return utils;
};

export default createTownUtils;
