// src/components/ui/Button.js
import React from 'react';

// ボタンの種類を定義
export const BUTTON_TYPES = {
  PRIMARY: 'primary',   // 主要なアクション用
  SECONDARY: 'secondary', // 補助的なアクション用
  DANGER: 'danger',     // 危険なアクション用
  SUCCESS: 'success',   // 成功/確認アクション用
  NEUTRAL: 'neutral'    // 中立的なアクション用
};

// 統一されたボタンコンポーネント
const Button = ({ 
  children, 
  onClick, 
  type = BUTTON_TYPES.PRIMARY, 
  disabled = false,
  fullWidth = false,
  className = '',
  icon = null
}) => {
  // ボタンタイプに基づくスタイルの定義
  const baseStyle = "py-2 px-4 rounded-lg font-medium transition-colors";
  
  // タイプ別のスタイル
  const typeStyles = {
    [BUTTON_TYPES.PRIMARY]: `bg-indigo-600 hover:bg-indigo-700 text-white ${disabled ? 'opacity-50' : ''}`,
    [BUTTON_TYPES.SECONDARY]: `bg-indigo-800 hover:bg-indigo-700 text-white ${disabled ? 'opacity-50' : ''}`,
    [BUTTON_TYPES.DANGER]: `bg-red-600 hover:bg-red-700 text-white ${disabled ? 'opacity-50' : ''}`,
    [BUTTON_TYPES.SUCCESS]: `bg-green-600 hover:bg-green-700 text-white ${disabled ? 'opacity-50' : ''}`,
    [BUTTON_TYPES.NEUTRAL]: `bg-yellow-600 hover:bg-yellow-700 text-white ${disabled ? 'opacity-50' : ''}`
  };
  
  // 幅のスタイル
  const widthStyle = fullWidth ? "w-full" : "";
  
  return (
    <button
      onClick={onClick}
      disabled={disabled}
      className={`${baseStyle} ${typeStyles[type]} ${widthStyle} ${className}`}
    >
      <div className="flex items-center justify-center">
        {icon && <span className="mr-2">{icon}</span>}
        {children}
      </div>
    </button>
  );
};

export default Button;
