// src/components/game/actions/CombatActionButtons.js
import { useState } from 'react';
import { useGame } from '../../../context/GameContext';

const CombatActionButtons = () => {
  const { 
    state, 
    log, 
    checkRoll,
    updateCharacter,
    updateDungeon,
    defeatEnemy,
    negotiateSuccess,
    gameOver
  } = useGame();
  
  const [isLoading, setIsLoading] = useState(false);
  
  const { dungeon, character } = state;
  
  // Helper to determine button disabled states
  const canAttack = dungeon.enemyPresent && !isLoading;
  const canUseMagic = dungeon.enemyPresent && character.mp >= 5 && !isLoading;
  const canNegotiate = dungeon.enemyPresent && !isLoading;
  
  // Handle attack
  const handleAttack = () => {
    setIsLoading(true);
    
    setTimeout(() => {
      if (!dungeon.enemyPresent || !dungeon.currentEnemy) {
        setIsLoading(false);
        return;
      }
      
      const enemy = dungeon.currentEnemy;
      
      // Player's attack
      const attackResult = checkRoll(
        character.stats.str,
        character.skills.sword,
        10
      );
      
      let enemyHp = enemy.hp;
      
      if (attackResult.critical) {
        const damage = character.stats.str * 2 + character.skills.sword * 2 - enemy.defense;
        enemyHp = Math.max(0, enemyHp - damage);
        log(`クリティカルヒット！${enemy.name}に${damage}ダメージ！`, 'critical');
      } else if (attackResult.success) {
        const damage = Math.max(1, character.stats.str + character.skills.sword - enemy.defense);
        enemyHp = Math.max(0, enemyHp - damage);
        log(`攻撃成功！${enemy.name}に${damage}ダメージ！`, 'success');
      } else if (attackResult.fumble) {
        log(`ファンブル！攻撃が大きく外れた！`, 'failure');
      } else {
        log(`攻撃が外れた！`, 'failure');
      }
      
      // Update enemy HP
      updateDungeon({ 
        currentEnemy: { 
          ...enemy, 
          hp: enemyHp 
        } 
      });
      
      // Enemy defeated?
      if (enemyHp <= 0) {
        defeatEnemy();
        setIsLoading(false);
        return;
      }
      
      // Enemy's counterattack
      const defenseResult = checkRoll(
        character.stats.dex,
        character.skills.sword,
        10
      );
      
      if (defenseResult.success) {
        log(`${enemy.name}の攻撃を回避した！`, 'success');
      } else {
        const damage = Math.max(1, enemy.attack - Math.floor(character.stats.str / 2));
        const newHp = Math.max(0, character.hp - damage);
        
        updateCharacter({ hp: newHp });
        
        log(`${enemy.name}の攻撃で${damage}ダメージを受けた！`, 'failure');
        
        if (newHp <= 0) {
          gameOver();
          setIsLoading(false);
          return;
        }
      }
      
      setIsLoading(false);
    }, 500);
  };
  
  // Handle magic
  const handleMagic = () => {
    setIsLoading(true);
    
    setTimeout(() => {
      if (!dungeon.enemyPresent || !dungeon.currentEnemy || character.mp < 5) {
        setIsLoading(false);
        return;
      }
      
      const mpCost = 5;
      updateCharacter({ mp: character.mp - mpCost });
      
      const enemy = dungeon.currentEnemy;
      
      // Magic attack
      const magicResult = checkRoll(
        character.stats.int,
        character.skills.magic,
        10
      );
      
      let enemyHp = enemy.hp;
      
      if (magicResult.critical) {
        const damage = character.stats.int * 2 + character.skills.magic * 2;
        enemyHp = Math.max(0, enemyHp - damage);
        log(`クリティカル魔法！${enemy.name}に${damage}ダメージ！`, 'critical');
      } else if (magicResult.success) {
        const damage = character.stats.int + character.skills.magic;
        enemyHp = Math.max(0, enemyHp - damage);
        log(`魔法攻撃が命中！${enemy.name}に${damage}ダメージ！`, 'success');
      } else if (magicResult.fumble) {
        const damage = Math.floor(character.stats.int / 2);
        const newHp = Math.max(0, character.hp - damage);
        
        updateCharacter({ hp: newHp });
        
        log(`ファンブル！魔法が暴走して自分に${damage}ダメージ！`, 'failure');
        
        if (newHp <= 0) {
          gameOver();
          setIsLoading(false);
          return;
        }
      } else {
        log(`魔法が失敗した！`, 'failure');
      }
      
      // Update enemy HP
      updateDungeon({ 
        currentEnemy: { 
          ...enemy, 
          hp: enemyHp 
        } 
      });
      
      // Enemy defeated?
      if (enemyHp <= 0) {
        defeatEnemy();
        setIsLoading(false);
        return;
      }
      
      // Enemy's counterattack
      const defenseResult = checkRoll(
        character.stats.dex,
        character.skills.trap,
        10
      );
      
      if (defenseResult.success) {
        log(`${enemy.name}の攻撃を回避した！`, 'success');
      } else {
        const damage = Math.max(1, enemy.attack - Math.floor(character.stats.int / 3));
        const newHp = Math.max(0, character.hp - damage);
        
        updateCharacter({ hp: newHp });
        
        log(`${enemy.name}の攻撃で${damage}ダメージを受けた！`, 'failure');
        
        if (newHp <= 0) {
          gameOver();
          setIsLoading(false);
          return;
        }
      }
      
      setIsLoading(false);
    }, 500);
  };
  
  // Handle negotiation
  const handleNegotiate = () => {
    setIsLoading(true);
    
    setTimeout(() => {
      if (!dungeon.enemyPresent || !dungeon.currentEnemy) {
        setIsLoading(false);
        return;
      }
      
      const enemy = dungeon.currentEnemy;
      
      // Negotiation check
      const negotiationResult = checkRoll(
        character.stats.mnd,
        character.skills.negotiation,
        10 + Math.floor(enemy.attack / 2)
      );
      
      if (negotiationResult.critical) {
        // Critical success
        log(`クリティカル交渉！${enemy.name}が味方になった！`, 'critical');
        
        negotiateSuccess(2);
      } else if (negotiationResult.success) {
        // Success
        log(`交渉成功！${enemy.name}は去っていった。`, 'success');
        
        negotiateSuccess(4);
      } else {
        // Failure - enemy attacks
        log(`交渉失敗！${enemy.name}は怒っている！`, 'failure');
        
        const damage = Math.max(1, enemy.attack - Math.floor(character.stats.mnd / 3));
        const newHp = Math.max(0, character.hp - damage);
        
        updateCharacter({ hp: newHp });
        
        log(`${enemy.name}の攻撃で${damage}ダメージを受けた！`, 'failure');
        
        if (newHp <= 0) {
          gameOver();
          setIsLoading(false);
          return;
        }
      }
      
      setIsLoading(false);
    }, 500);
  };
  
  // 戦闘中でない場合は表示しない
  if (!dungeon.enemyPresent) {
    return null;
  }
  
  return (
    <div className="grid grid-cols-3 gap-2">
      <button
        onClick={handleAttack}
        disabled={!canAttack}
        className={`py-2 px-4 rounded font-medium ${
          canAttack 
            ? 'bg-red-600 hover:bg-red-700 text-white' 
            : 'bg-indigo-900 text-indigo-400 cursor-not-allowed'
        } transition-colors`}
      >
        攻撃
      </button>
      
      <button
        onClick={handleMagic}
        disabled={!canUseMagic}
        className={`py-2 px-4 rounded font-medium ${
          canUseMagic 
            ? 'bg-blue-600 hover:bg-blue-700 text-white' 
            : 'bg-indigo-900 text-indigo-400 cursor-not-allowed'
        } transition-colors`}
      >
        魔法
      </button>
      
      <button
        onClick={handleNegotiate}
        disabled={!canNegotiate}
        className={`py-2 px-4 rounded font-medium ${
          canNegotiate 
            ? 'bg-purple-600 hover:bg-purple-700 text-white' 
            : 'bg-indigo-900 text-indigo-400 cursor-not-allowed'
        } transition-colors`}
      >
        交渉
      </button>
    </div>
  );
};

export default CombatActionButtons;
