// src/context/utils/index.js
/**
 * Main entry point for game utilities
 * Combines all utility modules
 */
import createLogUtils from './logUtils';
import createCharacterUtils from './characterUtils';
import createDungeonUtils from './dungeonUtils';
import createEnemyUtils from './enemyUtils';
import createProgressionUtils from './progressionUtils';
import createEventUtils from './eventUtils';
import createSaveUtils from './saveUtils';
import createTownUtils from './townUtils';
import createEquipmentUtils from './equipmentUtils';
import createQuestUtils from './questUtils';
import createConversationUtils from './conversationUtils';

/**
 * Create game utility functions
 * @param {Object} state - Current game state
 * @param {Function} dispatch - Reducer dispatch function
 * @returns {Object} Combined game utilities
 */
const createGameUtils = (state, dispatch) => {
  // 先に utils オブジェクトを宣言
  const utils = {};
  
  // Common utilities
  utils.rollDice = (sides = 10) => {
    return Math.floor(Math.random() * sides) + 1;
  };
  
  utils.checkRoll = (stat, skill, difficulty) => {
    const roll = utils.rollDice();
    const total = stat + skill + roll;
    return {
      success: total >= difficulty,
      critical: roll === 10,
      fumble: roll === 1,
      roll,
      total
    };
  };

  // 依存関係の順序を考慮してモジュールを作成
  Object.assign(utils, createLogUtils(state, dispatch));
  Object.assign(utils, createCharacterUtils(state, dispatch));
  
  // 経験値関連のユーティリティを最初に作成
  Object.assign(utils, createProgressionUtils(state, dispatch, utils));
  
  // クエストユーティリティを作成（他のユーティリティより先に）
  Object.assign(utils, createQuestUtils(state, dispatch, utils));
  
  // 会話ユーティリティを先に作成（townUtilsより前に）
  Object.assign(utils, createConversationUtils(state, dispatch, utils));
  
  // 依存関係を明示的に渡す
  Object.assign(utils, createDungeonUtils(state, dispatch, utils));
  Object.assign(utils, createEnemyUtils(state, dispatch, utils));
  
  // 残りのユーティリティを作成
  Object.assign(utils, createEventUtils(state, dispatch, utils));
  Object.assign(utils, createSaveUtils(state, dispatch, utils));
  Object.assign(utils, createTownUtils(state, dispatch, utils));
  Object.assign(utils, createEquipmentUtils(state, dispatch, utils));

  return utils;
};

export default createGameUtils;
