const itemDatabase = {
  weapons: [
    { id: 'basicSword', name: '基本の剣', attack: 3, weight: 1, price: 50, description: '冒険者の基本装備。' },
    { id: 'ironSword', name: '鉄の剣', attack: 5, weight: 2, price: 120, description: '標準的な鉄製の剣。' },
    { id: 'silverSword', name: '銀の剣', attack: 8, weight: 1, price: 300, description: '軽くて鋭い銀製の剣。' },
    { id: 'magicSword', name: '魔法の剣', attack: 12, weight: 1, price: 600, description: '魔力を帯びた強力な剣。' },
    { id: 'chiefSword', name: 'ゴブリンチーフの剣', attack: 7, weight: 2, price: 0, description: 'ゴブリンチーフが使っていた粗雑な剣。' }
  ],
  armours: [
    { id: 'clothArmour', name: '布の防具', defense: 2, weight: 0, price: 40, description: '基本的な布製の防具。' },
    { id: 'leatherArmour', name: '革の防具', defense: 4, weight: 1, price: 100, description: '丈夫な革製の防具。' },
    { id: 'chainMail', name: 'チェインメイル', defense: 7, weight: 3, price: 250, description: '鎖で編まれた丈夫な防具。' },
    { id: 'plateMail', name: 'プレートメイル', defense: 10, weight: 5, price: 500, description: '金属板で作られた重い防具。' },
    { id: 'guardianShield', name: '守護者の盾', defense: 8, weight: 2, price: 0, description: '古代の守護者が持っていた神秘的な盾。' }
  ],
  accessories: [
    { id: 'warriorRing', name: '戦士の指輪', effect: { stats: { str: 1 } }, price: 0, description: '戦士の力が宿った指輪。' },
    { id: 'ancientTalisman', name: '古代のタリスマン', effect: { stats: { int: 2, mnd: 1 } }, price: 0, description: '古代の知恵が宿ったタリスマン。' }
  ]
};

/**
 * アイテムIDからアイテムを検索する関数
 * @param {string} itemId - 検索するアイテムのID
 * @returns {Object|null} - 見つかったアイテムオブジェクト、または見つからない場合はnull
 */
export const findItemById = (itemId) => {
  for (const category of Object.keys(itemDatabase)) {
    const item = itemDatabase[category].find(item => item.id === itemId);
    if (item) return item;
  }
  return null;
};

export default itemDatabase; 
