// src/context/utils/equipmentUtils.js
import { ACTION_TYPES } from '../actions';

/**
 * 装備関連のユーティリティ関数を作成
 * @param {Object} state - 現在のゲームステート
 * @param {Function} dispatch - リデューサーディスパッチ関数
 * @param {Object} logUtils - ログユーティリティ関数
 * @returns {Object} 装備関連ユーティリティ関数
 */
const createEquipmentUtils = (state, dispatch, logUtils) => {
  const { log } = logUtils;

  return {
    /**
     * アイテムを装備する
     * @param {string} slot - 装備スロット ('weapon', 'armour')
     * @param {Object} item - 装備するアイテム
     */
    equipItem: (slot, item) => {
      // 以前の装備を取得
      const previousItem = state.character.equipment[slot];
      
      // キャラクターステータスの更新を準備
      let statUpdates = {};
      
      if (slot === 'weapon') {
        // 攻撃力の更新
        const previousAttack = previousItem ? previousItem.attack : 0;
        const attackDiff = item.attack - previousAttack;
        
        // 重さによる敏捷性への影響
        const previousWeight = previousItem ? previousItem.weight : 0;
        const weightDiff = item.weight - previousWeight;
        
        statUpdates = {
          equipment: {
            ...state.character.equipment,
            weapon: item
          }
        };
        
        log(`${item.name}を装備しました。攻撃力 ${attackDiff >= 0 ? '+' : ''}${attackDiff}`, 'success');
        
        if (weightDiff !== 0) {
          log(`武器の重さにより回避力 ${weightDiff <= 0 ? '+' : ''}${-weightDiff}`, weightDiff <= 0 ? 'success' : 'failure');
        }
      } else if (slot === 'armour') {
        // 防御力の更新
        const previousDefense = previousItem ? previousItem.defense : 0;
        const defenseDiff = item.defense - previousDefense;
        
        // 重さによる敏捷性への影響
        const previousWeight = previousItem ? previousItem.weight : 0;
        const weightDiff = item.weight - previousWeight;
        
        statUpdates = {
          equipment: {
            ...state.character.equipment,
            armour: item
          }
        };
        
        log(`${item.name}を装備しました。防御力 ${defenseDiff >= 0 ? '+' : ''}${defenseDiff}`, 'success');
        
        if (weightDiff !== 0) {
          log(`防具の重さにより回避力 ${weightDiff <= 0 ? '+' : ''}${-weightDiff}`, weightDiff <= 0 ? 'success' : 'failure');
        }
      }
      
      dispatch({
        type: ACTION_TYPES.UPDATE_CHARACTER,
        payload: statUpdates
      });
      
      // 以前の装備をインベントリに戻す
      if (previousItem) {
        createEquipmentUtils(state, dispatch, logUtils).addToInventory(previousItem);
      }
    },
    
    /**
     * 装備を外す
     * @param {string} slot - 装備スロット ('weapon', 'armour')
     */
    unequipItem: (slot) => {
      const item = state.character.equipment[slot];
      
      if (!item) {
        log(`${slot === 'weapon' ? '武器' : '防具'}は装備していません。`, 'failure');
        return;
      }
      
      // インベントリに追加
      createEquipmentUtils(state, dispatch, logUtils).addToInventory(item);
      
      // 装備を外す
      dispatch({
        type: ACTION_TYPES.UPDATE_CHARACTER,
        payload: {
          equipment: {
            ...state.character.equipment,
            [slot]: null
          }
        }
      });
      
      log(`${item.name}を外しました。`, 'normal');
    },
    
    /**
     * アイテムをインベントリに追加
     * @param {Object} item - 追加するアイテム
     */
    addToInventory: (item) => {
      dispatch({
        type: ACTION_TYPES.ADD_TO_INVENTORY,
        payload: item
      });
    },
    
    /**
     * アイテムをインベントリから削除
     * @param {string} itemId - 削除するアイテムのID
     */
    removeFromInventory: (itemId) => {
      dispatch({
        type: ACTION_TYPES.REMOVE_FROM_INVENTORY,
        payload: itemId
      });
    },
    
    /**
     * アイテムを購入
     * @param {Object} item - 購入するアイテム
     */
    buyItem: (item) => {
      if (state.character.gold < item.price) {
        log(`ゴールドが足りません。${item.price}Gが必要です。`, 'failure');
        return false;
      }
      
      // ゴールドを減らす
      dispatch({
        type: ACTION_TYPES.UPDATE_CHARACTER,
        payload: {
          gold: state.character.gold - item.price
        }
      });
      
      // インベントリに追加
      createEquipmentUtils(state, dispatch, logUtils).addToInventory(item);
      
      log(`${item.name}を${item.price}Gで購入しました。`, 'success');
      return true;
    },
    
    /**
     * アイテムを売却
     * @param {Object} item - 売却するアイテム
     */
    sellItem: (item) => {
      // 売却価格は購入価格の半分
      const sellPrice = Math.floor(item.price / 2);
      
      // ゴールドを増やす
      dispatch({
        type: ACTION_TYPES.UPDATE_CHARACTER,
        payload: {
          gold: state.character.gold + sellPrice
        }
      });
      
      // インベントリから削除
      createEquipmentUtils(state, dispatch, logUtils).removeFromInventory(item.id);
      
      log(`${item.name}を${sellPrice}Gで売却しました。`, 'success');
    },
    
    /**
     * キャラクターの攻撃力を計算（装備込み）
     * @returns {number} 総合攻撃力
     */
    calculateAttackPower: () => {
      const { character } = state;
      const baseAttack = character.stats.str;
      const weaponAttack = character.equipment.weapon ? character.equipment.weapon.attack : 0;
      
      return baseAttack + weaponAttack;
    },
    
    /**
     * キャラクターの防御力を計算（装備込み）
     * @returns {number} 総合防御力
     */
    calculateDefensePower: () => {
      const { character } = state;
      const baseDefense = Math.floor(character.stats.str / 2);
      const armourDefense = character.equipment.armour ? character.equipment.armour.defense : 0;
      
      return baseDefense + armourDefense;
    },
    
    /**
     * キャラクターの回避力を計算（装備の重さを考慮）
     * @returns {number} 回避力
     */
    calculateEvasion: () => {
      const { character } = state;
      const baseDex = character.stats.dex;
      const weaponWeight = character.equipment.weapon ? character.equipment.weapon.weight : 0;
      const armourWeight = character.equipment.armour ? character.equipment.armour.weight : 0;
      
      // 重い装備は回避力を下げる
      return Math.max(1, baseDex - weaponWeight - armourWeight);
    }
  };
};

export default createEquipmentUtils;
