// src/components/town/Tavern.js
import React, { useState } from 'react';
import { useGame } from '../../context/GameContext';
import ConversationPanel from './ConversationPanel';

const Tavern = ({ onBack }) => {
  const { state } = useGame();
  const [showRumors, setShowRumors] = useState(false);
  const [showTalk, setShowTalk] = useState(false);
  
  // 酒場の噂リスト
  const availableRumors = state.town.rumors || [];
  
  return (
    <div className="bg-indigo-900 rounded-lg shadow-lg p-4">
      <h3 className="text-xl font-semibold mb-4 text-sky-300">酒場「冒険者の休息」</h3>
      
      {!showRumors && !showTalk ? (
        <>
          <p className="mb-6">
            冒険者たちの憩いの場。中央にはカウンターがあり、きれいな看板娘があなたを迎え入れる。
            壁際のテーブルでは冒険者たちが酒を飲みながら話に花を咲かせています。
          </p>
          
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 mb-6">
            <button
              onClick={() => setShowRumors(true)}
              className="bg-indigo-800 hover:bg-indigo-700 rounded-lg p-4 text-left transition-colors"
            >
              <h4 className="font-semibold text-sky-300">うわさを聞く</h4>
              <p className="text-sm text-indigo-200">冒険者たちからダンジョンに関する情報を集める。</p>
            </button>
            
            <button
              onClick={() => setShowTalk(true)}
              className="bg-indigo-800 hover:bg-indigo-700 rounded-lg p-4 text-left transition-colors"
            >
              <h4 className="font-semibold text-sky-300">看板娘と話す</h4>
              <p className="text-sm text-indigo-200">美しい看板娘から街の情報を聞く。</p>
            </button>
          </div>
        </>
      ) : showRumors ? (
        <div className="rumor-list">
          <h4 className="font-semibold text-lg mb-4 text-amber-300">冒険者たちの話</h4>
          
          {availableRumors.length > 0 ? (
            <div className="space-y-4 mb-6">
              {availableRumors.map(rumor => (
                <div key={rumor.id} className="bg-indigo-800 p-4 rounded-lg">
                  <h5 className="font-medium text-sky-200 mb-2">{rumor.title}</h5>
                  <p className="text-indigo-200">{rumor.text}</p>
                </div>
              ))}
            </div>
          ) : (
            <p className="mb-6 text-indigo-300">今日は特に気になる話は聞こえてこない。</p>
          )}
          
          <button
            onClick={() => setShowRumors(false)}
            className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded-lg transition-colors"
          >
            戻る
          </button>
        </div>
      ) : showTalk ? (
        <ConversationPanel
          characterId="tavernGirl"
          onExitConversation={() => setShowTalk(false)}
        />
      ) : null}
      
      {/* 戻るボタン */}
      {!showRumors && !showTalk && (
        <button
          onClick={onBack}
          className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded-lg transition-colors"
        >
          広場へ戻る
        </button>
      )}
    </div>
  );
};

export default Tavern;
