// src/context/reducers/townReducer.js
import { ACTION_TYPES } from '../actions';

/**
 * 街の状態を管理するリデューサー
 * @param {Object} state - 現在の街の状態
 * @param {Object} action - 実行されるアクション
 * @returns {Object} 更新された街の状態
 */
export const townReducer = (state, action) => {
  switch (action.type) {
    case ACTION_TYPES.CHANGE_TOWN_LOCATION:
      return {
        ...state,
        currentLocation: action.payload
      };
      
    case ACTION_TYPES.UPDATE_SHOP_INVENTORY:
      return {
        ...state,
        shopInventory: {
          ...state.shopInventory,
          ...action.payload
        }
      };
      
    case ACTION_TYPES.ADD_RUMOR:
      // 既に同じIDの噂があるかチェック
      if (state.rumors.some(rumor => rumor.id === action.payload.id)) {
        return state;
      }
      
      return {
        ...state,
        rumors: [...state.rumors, action.payload]
      };

    case ACTION_TYPES.UPDATE_CONVERSATION:
      const { character, progressPoints, cooldowns } = action.payload;
      
      // 現在の会話データを取得（なければ初期化）
      const currentConversationData = state.conversations?.[character] || { 
        level: 0, 
        progress: 0,
        nextThreshold: 5,
        interactions: 0,
        cooldowns: {
          special: 0,
          secret: 0,
          personal: 0,
          favorite: 0
        }
      };
      
      // 進行度とインタラクション回数
      let totalProgress = currentConversationData.progress + progressPoints;
      const newInteractions = currentConversationData.interactions + 1;
      
      // レベルとしきい値の計算
      let newLevel = currentConversationData.level;
      let newThreshold = currentConversationData.nextThreshold;
      
      // レベルアップ処理（複数レベルアップの可能性も含む）
      while (totalProgress >= newThreshold && newLevel < 5) {
        totalProgress -= newThreshold;
        newLevel++;
        
        // レベルに応じて指数関数的に増加する閾値
        // 基本値5から始めて、レベルが上がるごとに倍率を上げる
        const baseThreshold = 5;
        const multiplier = newLevel * 1.5;
        newThreshold = Math.floor(baseThreshold * multiplier);
      }
      
      return {
        ...state,
        conversations: {
          ...state.conversations,
          [character]: {
            level: newLevel,
            progress: totalProgress,
            nextThreshold: newThreshold,
            interactions: newInteractions,
            cooldowns: cooldowns || currentConversationData.cooldowns
          }
        }
      };
      
    case ACTION_TYPES.RESET_SHOP_INVENTORY:
      return {
        ...state,
        shopInventory: {
          weapons: [],
          armours: []
        }
      };
      
    case ACTION_TYPES.PURCHASE_ITEM:
      // 購入したアイテムを在庫から削除
      const { itemId, itemType } = action.payload;
      
      return {
        ...state,
        shopInventory: {
          ...state.shopInventory,
          [itemType]: state.shopInventory[itemType].filter(item => item.id !== itemId)
        }
      };
      
    case ACTION_TYPES.UPDATE_TOWN_STATE:
      // 町の状態を一括で更新
      return {
        ...state,
        ...action.payload
      };
      
    default:
      return state;
  }
};

export default townReducer;
