// src/pages/QuestsPage.js
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useGame } from '../context/GameContext';
import ResponsivePageLayout from '../components/layout/ResponsivePageLayout';
import QuestBoard from '../components/quests/QuestBoard';
import QuestCard from '../components/quests/QuestCard';
import SaveLoadMenu from '../components/game/SaveLoadMenu';
import { QUEST_DATABASE } from '../data/quests';

const QuestsPage = () => {
    const { state, trackQuest } = useGame();
    const [activeTab, setActiveTab] = useState('active'); // 'active', 'completed', 'failed'
    const [showSaveMenu, setShowSaveMenu] = useState(false);
    const navigate = useNavigate();

    const [returnPath, setReturnPath] = useState('/town');

    // 前のページに戻るためのパスを保存
    useEffect(() => {
      const savedReturnPath = sessionStorage.getItem('questReturnPath');
      if (savedReturnPath) {
        setReturnPath(savedReturnPath);
      }
    }, []);

    // 戻るボタンの処理
    const handleClose = () => {
      navigate(returnPath);
    };

    // クエストの追跡処理
    const handleTrackQuest = (questId) => {
        trackQuest(questId === state.questJournal?.trackedQuestId ? null : questId);
    };

    // クエストデータベースからクエスト情報を取得
    const getQuestData = (questId) => {
        return QUEST_DATABASE.find(q => q.id === questId) || null;
    };

    // Action buttons for header
    const actionButtons = (
        <>
            <button
                onClick={() => handleClose()}
                className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded transition-colors min-w-[90px] md:min-w-[120px]"
            >
                閉じる
            </button>
            <button
                onClick={() => setShowSaveMenu(true)}
                className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded transition-colors min-w-[90px] md:min-w-[120px]"
            >
                メニュー
            </button>
        </>
    );

    // Tab buttons
    const tabButtons = (
        <div className="flex w-full mb-4 bg-indigo-800 rounded-lg">
            <button
                onClick={() => setActiveTab('active')}
                className={`flex-1 py-2 px-3 rounded-l-lg ${
                    activeTab === 'active'
                        ? 'bg-indigo-600 text-white'
                        : 'text-indigo-300 hover:bg-indigo-700'
                }`}
            >
                進行中
            </button>
            <button
                onClick={() => setActiveTab('completed')}
                className={`flex-1 py-2 px-3 ${
                    activeTab === 'completed'
                        ? 'bg-indigo-600 text-white'
                        : 'text-indigo-300 hover:bg-indigo-700'
                }`}
            >
                完了済み
            </button>
            <button
                onClick={() => setActiveTab('available')}
                className={`flex-1 py-2 px-3 rounded-r-lg ${
                    activeTab === 'available'
                        ? 'bg-indigo-600 text-white'
                        : 'text-indigo-300 hover:bg-indigo-700'
                }`}
            >
                利用可能
            </button>
        </div>
    );

    // 現在のタブに表示するコンテンツを取得
    const renderContent = () => {
        switch (activeTab) {
            case 'active':
                // 進行中のクエスト一覧
                const activeQuests = state.questJournal?.activeQuests || [];
                return (
                    <div>
                        <h3 className="text-lg font-semibold text-sky-300 px-4 py-2 bg-indigo-800 mb-4">
                            進行中のクエスト
                        </h3>
                        {activeQuests.length > 0 ? (
                            <div className="space-y-2 p-4">
                                {activeQuests.map(quest => {
                                    const questData = getQuestData(quest.id);
                                    if (!questData) return null;
                                    
                                    return (
                                        <QuestCard
                                            key={quest.id}
                                            quest={quest}
                                            questData={questData}
                                            isTracked={quest.id === state.questJournal?.trackedQuestId}
                                            onTrack={() => handleTrackQuest(quest.id)}
                                        />
                                    );
                                })}
                            </div>
                        ) : (
                            <div className="text-center p-4 text-indigo-300">
                                現在進行中のクエストはありません
                            </div>
                        )}
                    </div>
                );
            
            case 'completed':
                // 完了済みクエスト一覧
                const completedQuestIds = state.questJournal?.completedQuests || [];
                return (
                    <div>
                        <h3 className="text-lg font-semibold text-sky-300 px-4 py-2 bg-indigo-800 mb-4">
                            完了済みクエスト
                        </h3>
                        {completedQuestIds.length > 0 ? (
                            <div className="space-y-2 p-4">
                                {completedQuestIds.map(questId => {
                                    const questData = getQuestData(questId);
                                    if (!questData) return null;
                                    
                                    return (
                                        <div key={questId} className="bg-indigo-800 p-4 rounded-lg">
                                            <div className="flex justify-between items-start">
                                                <h3 className="text-lg font-semibold text-green-300">{questData.title}</h3>
                                                <div className="text-xs bg-green-600 px-2 py-1 rounded">
                                                    完了済み
                                                </div>
                                            </div>
                                            <p className="text-sm text-indigo-200 mt-2">{questData.description}</p>
                                        </div>
                                    );
                                })}
                            </div>
                        ) : (
                            <div className="text-center p-4 text-indigo-300">
                                完了したクエストはありません
                            </div>
                        )}
                    </div>
                );
            
            case 'available':
                // 利用可能なクエスト一覧（クエスト掲示板）
                return (
                    <div>
                        <h3 className="text-lg font-semibold text-sky-300 px-4 py-2 bg-indigo-800 mb-4">
                            利用可能なクエスト
                        </h3>
                        <div className="px-4">
                            <QuestBoard />
                        </div>
                    </div>
                );
            
            default:
                return <div>未知のタブ</div>;
        }
    };

    return (
        <>
            <ResponsivePageLayout
                title="クエスト"
                actions={actionButtons}
                showCharacterSheet={false}
                showGameLog={false}
            >
                {tabButtons}

                <div className="bg-indigo-900 rounded-lg shadow-lg overflow-hidden">
                    {renderContent()}
                </div>
            </ResponsivePageLayout>

            {/* セーブ/ロードメニュー */}
            {showSaveMenu && (
                <SaveLoadMenu onClose={() => setShowSaveMenu(false)} />
            )}
        </>
    );
};

export default QuestsPage;
