// src/context/utils/conversationUtils.js
import { ACTION_TYPES } from '../actions';
import { findItemById } from '../../data/itemDatabase';

// 会話データをインポート
import { tavernGirlData } from '../../data/conversations/tavernGirl';
import { weaponShopGirlData } from '../../data/conversations/weaponShopGirl';
import { armourShopGirlData } from '../../data/conversations/armourShopGirl';

// 必要なインポートを追加
import { getConversationEvent } from '../../data/conversationEvents';

/**
 * 会話関連のユーティリティ関数を作成
 * @param {Object} state - 現在のゲーム状態
 * @param {Function} dispatch - リデューサーのディスパッチ関数
 * @param {Object} utils - ユーティリティ関数
 * @returns {Object} 会話関連のユーティリティ関数
 */
const createConversationUtils = (state, dispatch, utils) => {
  const { log } = utils;
  
  // 先に conversationUtils オブジェクトを宣言
  const conversationUtils = {};

  // キャラクターデータを取得
  conversationUtils.getCharacterData = (characterId) => {
    switch (characterId) {
      case 'tavernGirl': return tavernGirlData;
      case 'weaponShopGirl': return weaponShopGirlData;
      case 'armourShopGirl': return armourShopGirlData;
      default: return null;
    }
  };
  
  // 親密度レベルに基づいてレベル会話データを取得
  conversationUtils.getLevelConversation = (characterId) => {
    const data = conversationUtils.getCharacterData(characterId);
    if (!data || !data.levelConversations) return null;
    
    // 現在の会話データを取得
    const conversationData = state.town.conversations[characterId] || { level: 0 };
    const level = conversationData.level || 0;
    
    // データが配列の場合（新しい形式）
    if (Array.isArray(data.levelConversations)) {
      // レベルが範囲内なら対応するレベルの会話を返す
      if (level < data.levelConversations.length) {
        return data.levelConversations[level];
      }
      // レベルが範囲外なら最後のレベルの会話を返す
      return data.levelConversations[data.levelConversations.length - 1];
    }
    
    // データがオブジェクトの場合（古い形式）
    return data.levelConversations[level] || data.levelConversations[0];
  };

  // 挨拶を取得
  conversationUtils.getGreeting = (characterId) => {
    const levelConversation = conversationUtils.getLevelConversation(characterId);
    
    if (levelConversation && levelConversation.greeting) {
      let greeting = levelConversation.greeting;
      
      // キャラクター名の置換（もし{characterName}があれば）
      if (greeting.includes('{characterName}')) {
        greeting = greeting.replace('{characterName}', state.character.name);
      }
      
      return greeting;
    }
    return "いらっしゃい。";
  };

  // 会話オプションを取得する関数
  conversationUtils.getConversationOptions = (characterId) => {
    const data = conversationUtils.getCharacterData(characterId);
    
    if (!data) return [];
    
    // 会話データを取得
    const conversationData = state.town.conversations[characterId] || { level: 0 };
    const level = conversationData.level || 0;
    
    // すべてのレベルの会話オプションを収集
    let options = [];
    
    if (data.levelConversations && Array.isArray(data.levelConversations)) {
      for (let i = 0; i <= Math.min(level, data.levelConversations.length - 1); i++) {
        if (data.levelConversations[i] && data.levelConversations[i].options) {
          options.push(...data.levelConversations[i].options);
        }
      }
    } 
    
    // 特別会話オプション（条件を満たすもの）
    if (data.specialConversations) {
      data.specialConversations.forEach(special => {
        if (conversationUtils.evaluateCondition(special.condition) && 
            !options.some(o => o.id === special.id)) {
          options.push({
            id: special.id,
            text: special.text,
            type: special.type || 'special',
            response: special.response
          });
        }
      });
    }
    
    // タイプに基づくフィルタリングとクールダウンチェック
    return options.filter(option => {
      // タイプに基づくフィルタリング
      const typeAllowed = 
        !option.type || 
        option.type === 'normal' || 
        option.type === 'favorite' ||
        (option.type === 'special' && level >= 2) ||
        (option.type === 'secret' && level >= 3) ||
        (option.type === 'personal' && level >= 4);
      
      if (!typeAllowed) return false;
      
      // クールダウンチェック（通常会話はクールダウンなし）
      if (!option.type || option.type === 'normal') return true;
      return conversationUtils.checkCooldown(characterId, option.type);
    });
  };
  
  // 宣言的な条件を評価するヘルパー関数
  conversationUtils.evaluateCondition = (condition) => {
    // 関数の場合は直接実行
    if (typeof condition === 'function') {
      return condition(state);
    }
    
    // オブジェクトでない場合は常にtrue
    if (!condition || typeof condition !== 'object') {
      return true;
    }
    
    // 演算子に基づいて値を比較する共通関数
    const compareValues = (actual, expected, operator = 'gte') => {
      switch (operator) {
        case 'eq': return actual === expected;
        case 'neq': return actual !== expected;
        case 'gt': return actual > expected;
        case 'gte': return actual >= expected;
        case 'lt': return actual < expected;
        case 'lte': return actual <= expected;
        default: return actual >= expected; // デフォルトはgte
      }
    };
    
    // 条件タイプに基づいて評価
    switch (condition.type) {
      case 'stat_check': {
        // ステータスチェック
        const { stat, value, operator } = condition;
        let actualValue;
        
        // 防御力の場合は計算
        if (stat === 'defense') {
          actualValue = state.character.stats.str / 2 + (state.character.equipment?.armour?.defense || 0);
        } else {
          // その他のステータス
          actualValue = state.character.stats[stat] || 0;
        }
        
        return compareValues(actualValue, value, operator);
      }
      
      case 'completed_dungeons': {
        // ダンジョン攻略数チェック
        const { count, operator } = condition;
        const completedCount = state.character?.completedDungeons?.length || 0;
        
        return compareValues(completedCount, count, operator);
      }
      
      case 'has_item': {
        // アイテム所持チェック
        const { itemId, category } = condition;
        
        if (category) {
          // カテゴリ指定がある場合
          return state.character.inventory[category]?.some(item => item.id === itemId) || false;
        } else {
          // 全カテゴリ検索
          return Object.values(state.character.inventory).some(items => 
            items.some(item => item.id === itemId)
          ) || false;
        }
      }
      
      case 'quest_status': {
        // クエスト状態チェック
        const { questId, status } = condition;
        const quest = state.quests.find(q => q.id === questId);
        
        if (!quest) return false;
        
        switch (status) {
          case 'active': return quest.status === 'active';
          case 'completed': return quest.status === 'completed';
          case 'failed': return quest.status === 'failed';
          default: return false;
        }
      }
      
      case 'character_level': {
        // キャラクターレベルチェック
        const { level, operator } = condition;
        const characterLevel = state.character.level || 1;
        
        return compareValues(characterLevel, level, operator);
      }
      
      case 'and': {
        // AND条件（すべての条件を満たす）
        return condition.conditions.every(subCondition => 
          conversationUtils.evaluateCondition(subCondition)
        );
      }
      
      case 'or': {
        // OR条件（いずれかの条件を満たす）
        return condition.conditions.some(subCondition => 
          conversationUtils.evaluateCondition(subCondition)
        );
      }
      
      case 'not': {
        // NOT条件（条件を満たさない）
        return !conversationUtils.evaluateCondition(condition.condition);
      }
      
      case 'has_equipment': {
        // 特定のスロットに装備があるかチェック
        const { slot } = condition;
        return state.character.equipment && state.character.equipment[slot] !== null;
      }
      
      case 'gold_amount': {
        // 所持金チェック
        const { amount, operator } = condition;
        const gold = state.character.gold || 0;
        
        return compareValues(gold, amount, operator);
      }
      
      case 'skill_level': {
        // スキルレベルチェック
        const { skill, level, operator } = condition;
        const skillLevel = state.character.skills?.[skill] || 0;
        
        return compareValues(skillLevel, level, operator);
      }
      
      default:
        console.warn(`未知の条件タイプ: ${condition.type}`);
        return false;
    }
  };

  // 会話応答を取得
  conversationUtils.getResponse = (characterId, conversationId) => {
    const options = conversationUtils.getConversationOptions(characterId);
    const option = options.find(opt => opt.id === conversationId);
    const characterData = conversationUtils.getCharacterData(characterId);
    
    let response = "...";
    
    // 通常の応答があれば使用
    if (option && option.response) {
      response = option.response;
    }
    // 特別会話オプションの応答を探す（直接オプションから見つからなければ）
    else if (characterData && characterData.specialConversations) {
      const specialConversation = characterData.specialConversations.find(
        special => special.id === conversationId
      );
      if (specialConversation && specialConversation.response) {
        response = specialConversation.response;
      }
    }
    // バリエーション応答があれば使用
    else if (characterData && characterData.responseVariants && characterData.responseVariants[conversationId]) {
      const variants = characterData.responseVariants[conversationId];
      
      // 親密度レベルに応じて選択（レベルが高いほど深い応答）
      const conversationLevel = state.town.conversations[characterId]?.level || 0;
      let index = 0;
      
      if (variants.length > 1) {
        // レベルに応じた応答選択（親密度が上がるほど深い応答に）
        if (conversationLevel >= variants.length) {
          // レベルが高ければランダムな応答
          index = Math.floor(Math.random() * variants.length);
        } else {
          // レベル単位で応答を深める
          index = Math.min(conversationLevel, variants.length - 1);
        }
      }
      
      response = variants[index];
    }
    
    // キャラクター名の置換
    if (response.includes('{characterName}')) {
      response = response.replace('{characterName}', state.character.name);
    }
    
    return response;
  };

  // クールダウンチェック関数
  conversationUtils.checkCooldown = (characterId, conversationType) => {
    const characterData = state.town.conversations[characterId];
    if (!characterData || !characterData.cooldowns) return false;
    
    // 現在のインタラクション回数
    const currentInteractions = characterData.interactions || 0;
    
    // 最後にこのタイプの会話をした時のインタラクション回数
    const lastInteraction = characterData.cooldowns[conversationType] || 0;
    
    // 必要なインタラクション回数の差（クールダウン期間）
    const requiredInteractions = conversationUtils.getRequiredInteractions(conversationType);
    
    // インタラクション回数の差がクールダウン期間より大きければ利用可能
    return (currentInteractions - lastInteraction) >= requiredInteractions;
  };
  
  // 会話タイプごとの必要インタラクション回数を取得
  conversationUtils.getRequiredInteractions = (conversationType) => {
    switch (conversationType) {
      case 'special': return 3;  // 特別な会話
      case 'secret': return 5;   // 秘密の会話
      case 'personal': return 4; // 個人的な会話
      case 'favorite': return 2; // お気に入りの話題
      default: return 0;         // 通常の会話はクールダウンなし
    }
  };
  
  // クールダウンを更新する関数を修正
  conversationUtils.updateCooldowns = (characterId, conversationType) => {
    const characterData = state.town.conversations[characterId] || {
      level: 0,
      progress: 0,
      nextThreshold: 5,
      interactions: 0,
      cooldowns: {}
    };
    
    // 現在のインタラクション回数を取得
    const currentInteractions = characterData.interactions || 0;
    
    // クールダウンを更新（現在のインタラクション回数を記録）
    const updatedCooldowns = {
      ...characterData.cooldowns,
      [conversationType]: currentInteractions
    };
    
    return updatedCooldowns;
  };
  
  // 会話を進める関数
  conversationUtils.advanceConversation = (characterId, conversationId, conversationType) => {
    // 会話前の親密度レベルを記録
    const prevConversationData = state.town.conversations[characterId] || { 
      level: 0, 
      progress: 0,
      nextThreshold: 5,
      interactions: 0,
      // タイプごとのクールダウン情報を保持
      cooldowns: {
        special: 0,
        secret: 0,
        personal: 0,
        favorite: 0
      }
    };
    const prevLevel = prevConversationData.level || 0;
    
    // 応答を取得して表示
    const response = conversationUtils.getResponse(characterId, conversationId);
    
    // キャラクターデータと名前を取得
    const data = conversationUtils.getCharacterData(characterId);
    const name = data ? (data.title ? `${data.name}（${data.title}）` : data.name) : "???";
    
    // ログに応答を表示
    log(`${name}: ${response}`);
    
    // 会話タイプに基づいて進行ポイントを決定
    let progressPoints = 1;
    switch (conversationType) {
      case 'favorite': progressPoints = 2; break;
      case 'special': progressPoints = 3; break;
      case 'secret': progressPoints = 4; break;
      case 'personal': progressPoints = 3; break;
      default: progressPoints = 1;
    }
    
    // クールダウンを更新
    const updatedCooldowns = conversationUtils.updateCooldowns(characterId, conversationType);
    
    // インタラクション回数を増加させる
    dispatch({
      type: ACTION_TYPES.UPDATE_CONVERSATION,
      payload: {
        character: characterId,
        progressPoints,
        cooldowns: updatedCooldowns
      }
    });
    
    // 会話イベントをトリガー
    conversationUtils.triggerConversationEvent(characterId, conversationId);
    
    // 会話関連のクエスト目標を更新
    if (utils.updateQuestObjectivesByTrigger) {
      utils.updateQuestObjectivesByTrigger('talk_to_npc', { 
        characterId: characterId, 
        conversationId: conversationId 
      });
    }
    
    // 親密度レベルが上がったかチェック
    setTimeout(() => {
      const currentConversationData = state.town.conversations[characterId] || { level: 0 };
      const currentLevel = currentConversationData.level || 0;
      
      if (currentLevel > prevLevel) {
        // 親密度レベルに応じた関係性の変化を表示
        let relationshipChange = '';
        if (currentLevel === 1) {
          relationshipChange = `${name}はあなたのことを覚えてくれました。`;
        } else if (currentLevel === 2) {
          relationshipChange = `${name}はあなたを知り合いとして扱ってくれるようになりました。`;
        } else if (currentLevel === 3) {
          relationshipChange = `${name}はあなたを友人として見てくれるようになりました。`;
        } else if (currentLevel === 4) {
          relationshipChange = `${name}はあなたを信頼してくれるようになりました。`;
        } else if (currentLevel >= 5) {
          relationshipChange = `${name}はあなたを特別な存在として見てくれるようになりました。`;
        }
        
        log(`親密度がレベル${currentLevel}に上昇しました！${relationshipChange}`, 'critical');
      }
    }, 10);
    
    return response;
  };
  
  // 会話イベントトリガー
  conversationUtils.triggerConversationEvent = (characterId, conversationId) => {
    // 設定ファイルからイベントを取得
    const event = getConversationEvent(characterId, conversationId);
    
    // イベントがなければ何もしない
    if (!event) return;
    
    // イベントタイプに応じた処理
    switch (event.type) {
      case 'add_shop_item': {
        // ショップアイテムの追加
        const { itemId, category, price, text } = event.data;
        const item = findItemById(itemId);
        
        if (item) {
          dispatch({
            type: ACTION_TYPES.UPDATE_SHOP_INVENTORY,
            payload: { 
              [category]: [...(state.town.shopInventory[category] || []), {...item, price}]
            }
          });
          
          if (text) {
            log(text, 'success');
          }
        }
        break;
      }
        
      case 'add_rumor': {
        // うわさの追加
        const { rumorId, title, description, text, triggerQuestUpdate } = event.data;
        
        // すでに発見済みのうわさでなければ追加
        if (!state.character.discoveredRumors || 
            !state.character.discoveredRumors.includes(rumorId)) {
          
          dispatch({
            type: ACTION_TYPES.ADD_RUMOR,
            payload: {
              id: rumorId,
              title,
              text: description
            }
          });
          
          if (text) {
            log(text, 'success');
          }
          
          // クエスト更新トリガーがあれば実行
          if (triggerQuestUpdate && utils.updateQuestObjectivesByTrigger) {
            utils.updateQuestObjectivesByTrigger(
              triggerQuestUpdate.triggerType,
              {
                ...triggerQuestUpdate.data,
                characterId: characterId,
                conversationId: conversationId
              }
            );
          }
        }
        break;
      }
        
      default:
        console.warn(`未知のイベントタイプ: ${event.type}`);
    }
  };
  
  // 会話の進行度情報を取得
  conversationUtils.getConversationProgress = (characterId) => {
    const conversationData = state.town.conversations[characterId] || { 
      level: 0, 
      progress: 0,
      nextThreshold: 5,
      interactions: 0
    };
    
    // キャラクターデータを取得
    const data = conversationUtils.getCharacterData(characterId);
    const name = data ? data.name : characterId;
    const title = data ? data.title || '' : '';
    
    // 親密度レベルに基づく関係ステータスを取得
    let relationshipStatus = '見知らぬ人';
    let relationshipDescription = `${name}はあなたのことをまだ知りません。`;
    
    if (conversationData.level >= 5) {
      relationshipStatus = '特別な関係';
      relationshipDescription = `${name}はあなたを特別な存在として見ています。`;
    } else if (conversationData.level >= 4) {
      relationshipStatus = '信頼できる友人';
      relationshipDescription = `${name}はあなたを深く信頼しています。`;
    } else if (conversationData.level >= 3) {
      relationshipStatus = '友人';
      relationshipDescription = `${name}はあなたを友人として扱っています。`;
    } else if (conversationData.level >= 2) {
      relationshipStatus = '知り合い';
      relationshipDescription = `${name}はあなたのことを知り合いとして認識しています。`;
    } else if (conversationData.level >= 1) {
      relationshipStatus = '面識あり';
      relationshipDescription = `${name}はあなたの顔と名前を覚えています。`;
    }
    
    // 次のレベルまでのポイント残り
    const remainingPoints = conversationData.nextThreshold - conversationData.progress;
    
    return {
      characterInfo: {
        name,
        title,
        fullTitle: title ? `${name}（${title}）` : name
      },
      level: conversationData.level,
      progress: conversationData.progress,
      nextThreshold: conversationData.nextThreshold,
      remainingPoints,
      progressPercent: Math.min(100, (conversationData.progress / conversationData.nextThreshold) * 100),
      interactions: conversationData.interactions,
      relationshipStatus,
      relationshipDescription
    };
  };
  
  // キャラクターとの関係性概要を取得
  conversationUtils.getRelationshipSummary = (characterId) => {
    const progress = conversationUtils.getConversationProgress(characterId);
    
    return {
      character: progress.characterInfo.fullTitle,
      level: progress.level,
      status: progress.relationshipStatus,
      description: progress.relationshipDescription,
      nextLevel: progress.level < 5 ? 
        `次のレベルまであと${progress.remainingPoints}ポイント（${Math.floor(progress.progressPercent)}%達成）` :
        '最高レベルに達しています'
    };
  };

  return conversationUtils;
};

export default createConversationUtils;
