import React, { useState, useEffect } from 'react';
import { useGame } from '../../context/GameContext';
import { heartEmoji } from '../../utils/uiHelpers';
import { QUEST_DATABASE } from '../../data/quests';
import QuestRewardDialog from '../quests/QuestRewardDialog';

const ConversationPanel = ({ characterId, onExitConversation }) => {
  const { 
    state, 
    getCharacterData, 
    getGreeting, 
    getConversationOptions,
    advanceConversation,
    getConversationProgress,
    getQuestsFromGiver,
    getCompletableQuestsForGiver,
    startQuest
  } = useGame();
  
  const [currentResponse, setCurrentResponse] = useState(null);
  const [showQuestOptions, setShowQuestOptions] = useState(false);
  const [availableQuests, setAvailableQuests] = useState([]);
  const [completableQuests, setCompletableQuests] = useState([]);
  const [showQuestReward, setShowQuestReward] = useState(false);
  const [completingQuestId, setCompletingQuestId] = useState(null);
  
  // キャラクターデータを取得
  const characterData = getCharacterData(characterId);
  // 進行度情報を取得
  const progressInfo = getConversationProgress(characterId);
  // 挨拶を取得
  const greeting = getGreeting(characterId);
  // 会話オプションを取得
  const conversationOptions = getConversationOptions(characterId);
  
  // 初期化時とキャラID変更時にクエスト情報を取得
  useEffect(() => {
    if (characterId) {
      // キャラクターIDに基づいて利用可能なクエストと完了可能なクエストを取得
      const quests = getQuestsFromGiver ? getQuestsFromGiver(characterId) : [];
      const completable = getCompletableQuestsForGiver ? getCompletableQuestsForGiver(characterId) : [];
      
      setAvailableQuests(quests);
      setCompletableQuests(completable);
    }
  }, [characterId, state.questJournal]);
  
  // 会話オプションを選択したときの処理
  const handleSelectOption = (conversationId) => {
    if (conversationId === 'show_quests') {
      // クエスト一覧を表示するオプション
      setShowQuestOptions(true);
      setCurrentResponse(`何か手伝って欲しいことがあるなら、言ってくれる？`);
    } else {
      // 選択されたオプションの会話タイプを取得
      const selectedOption = conversationOptions.find(option => option.id === conversationId);
      const conversationType = selectedOption?.type || 'normal';
      
      // 会話タイプを渡して会話を進める
      const response = advanceConversation(characterId, conversationId, conversationType);
      setCurrentResponse(response);
      setShowQuestOptions(false);
    }
  };
  
  // クエストを受注する処理
  const handleAcceptQuest = (questId) => {
    const quest = availableQuests.find(q => q.id === questId);
    if (quest && startQuest) {
      startQuest(questId);
      setCurrentResponse(`${quest.title}を依頼するわ。よろしくお願いね！`);
      setShowQuestOptions(false);
      
      // クエストリストを更新
      const updatedQuests = availableQuests.filter(q => q.id !== questId);
      setAvailableQuests(updatedQuests);
    }
  };
  
  // クエストを完了する処理
  const handleCompleteQuest = (questId) => {
    const quest = completableQuests.find(q => q.id === questId);
    if (quest) {
      setCompletingQuestId(questId);
      setShowQuestReward(true);
      setCurrentResponse(`素晴らしい！任務を完遂してくれたのね！報酬はちゃんと用意してあるわよ。`);
      setShowQuestOptions(false);
      
      // クエストリストを更新
      const updatedCompletable = completableQuests.filter(q => q.id !== questId);
      setCompletableQuests(updatedCompletable);
    }
  };
  
  // 報酬ダイアログを閉じる処理
  const handleCloseRewardDialog = () => {
    setShowQuestReward(false);
    setCompletingQuestId(null);
  };
  
  // コンポーネントマウント時に挨拶をセット
  useEffect(() => {
    setCurrentResponse(greeting);
  }, [greeting]);
  
  // クエスト関連の会話オプションを追加
  const hasQuests = availableQuests.length > 0 || completableQuests.length > 0;
  const questOption = hasQuests ? [
    {
      id: 'show_quests',
      text: `${characterData.name}からの依頼について話す${
        completableQuests.length > 0 ? ' (完了報告あり)' : ''
      }`
    }
  ] : [];
  
  // 通常会話オプションにクエストオプションを追加
  const allOptions = [...conversationOptions, ...questOption];
  
  // クエストデータを取得する関数
  const getQuestTitle = (questId) => {
    const questData = QUEST_DATABASE.find(q => q.id === questId);
    return questData ? questData.title : questId;
  };
  
  if (!characterData) return <div>キャラクターデータが見つかりません</div>;
  
  return (
    <div className="bg-indigo-800 p-4 rounded-lg mb-6">
      <div className="flex justify-between items-start mb-4">
        <h4 className="font-semibold text-lg text-amber-300">{characterData.name} - {characterData.title}</h4>
        <div className="flex items-center">
          <span className="text-xs bg-indigo-700 px-2 py-1 rounded">
            親密度: {heartEmoji(progressInfo.level)}
          </span>
          <div className="ml-2 w-16 h-2 bg-indigo-900 rounded-full overflow-hidden">
            <div 
              className="h-full bg-pink-500 transition-all duration-300"
              style={{ width: `${progressInfo.progressPercent}%` }}
            ></div>
          </div>
        </div>
      </div>
      
      <div className="mb-4 p-3 bg-indigo-900/50 rounded">
        <p className="text-indigo-200">
          {currentResponse || greeting}
        </p>
      </div>
      
      {/* クエスト選択画面 */}
      {showQuestOptions ? (
        <div className="space-y-3 mb-4">
          <h5 className="font-medium text-sky-300">依頼一覧</h5>
          
          {completableQuests.length > 0 && (
            <div className="space-y-2">
              <h6 className="text-xs font-medium text-green-300">完了報告可能</h6>
              {completableQuests.map(quest => (
                <button
                  key={quest.id}
                  onClick={() => handleCompleteQuest(quest.id)}
                  className="w-full text-left bg-green-700 hover:bg-green-600 p-3 rounded-lg transition-colors"
                >
                  <span className="font-medium text-white">クエスト {getQuestTitle(quest.id)}を報告する</span>
                </button>
              ))}
            </div>
          )}
          
          {availableQuests.length > 0 && (
            <div className="space-y-2">
              <h6 className="text-xs font-medium text-sky-300">新規依頼</h6>
              {availableQuests.map(quest => (
                <button
                  key={quest.id}
                  onClick={() => handleAcceptQuest(quest.id)}
                  className="w-full text-left bg-indigo-700 hover:bg-indigo-600 p-3 rounded-lg transition-colors"
                >
                  <div className="font-medium">{quest.title}</div>
                  <div className="text-xs text-indigo-300 mt-1">{quest.description}</div>
                </button>
              ))}
            </div>
          )}
          
          {availableQuests.length === 0 && completableQuests.length === 0 && (
            <p className="text-indigo-300 text-sm">現在、新しい依頼はありません。</p>
          )}
          
          <button
            onClick={() => setShowQuestOptions(false)}
            className="w-full text-left bg-indigo-800 hover:bg-indigo-700 p-2 rounded-lg transition-colors text-sm"
          >
            他の話題について話す
          </button>
        </div>
      ) : (
        <div className="space-y-3 mb-4">
          {allOptions.map(option => {
            // 会話タイプに基づいて背景色を決定
            let bgColorClass = 'bg-indigo-700 hover:bg-indigo-600';
            
            // タイプに応じた色を適用
            if (option.id === 'show_quests') {
              // クエスト選択肢
              bgColorClass = 'bg-purple-700 hover:bg-purple-600';
            } else if (option.type === 'favorite') {
              // お気に入りの話題
              bgColorClass = 'bg-pink-700 hover:bg-pink-600';
            } else if (option.type === 'special') {
              // 特別な会話
              bgColorClass = 'bg-amber-700 hover:bg-amber-600';
            } else if (option.type === 'secret') {
              // 秘密の会話
              bgColorClass = 'bg-emerald-700 hover:bg-emerald-600';
            } else if (option.type === 'personal') {
              // 個人的な会話
              bgColorClass = 'bg-rose-700 hover:bg-rose-600';
            }
            
            return (
              <button
                key={option.id}
                onClick={() => handleSelectOption(option.id)}
                className={`w-full text-left ${bgColorClass} p-3 rounded-lg transition-colors`}
              >
                {option.text}
                
                {option.id === 'show_quests' && completableQuests.length > 0 && (
                  <span className="ml-2 px-1.5 py-0.5 bg-green-600 text-xs rounded-full">
                    {completableQuests.length}
                  </span>
                )}
              </button>
            );
          })}
        </div>
      )}
      
      <div className="text-center">
        <button
          onClick={onExitConversation}
          className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded-lg transition-colors"
        >
          会話を終える
        </button>
      </div>
      
      {/* 報酬ダイアログ */}
      {showQuestReward && completingQuestId && (
        <QuestRewardDialog 
          questId={completingQuestId} 
          onClose={handleCloseRewardDialog} 
        />
      )}
    </div>
  );
};

export default ConversationPanel;
