// src/components/town/TownSquare.js
import React from 'react';
import LocationButton from './LocationButton';

const TownSquare = ({ onLocationChange, onDungeonSelect }) => {
  return (
    <div className="bg-indigo-900 rounded-lg shadow-lg p-4">
      <h3 className="text-xl font-semibold mb-4 text-sky-300">街の広場</h3>
      
      <p className="mb-6">
        冒険者たちで賑わう街の中心広場。さまざまな施設があなたの訪問を待っています。
      </p>
      
      <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 mb-6">
        <LocationButton
          name="酒場「冒険者の休息」"
          description="うわさ話を聞いたり、他の冒険者と交流できる場所。"
          onClick={() => onLocationChange('tavern')}
        />
        
        <LocationButton
          name="武器屋「鋼の意志」"
          description="様々な武器を取り扱う店。"
          onClick={() => onLocationChange('weaponShop')}
        />
        
        <LocationButton
          name="防具屋「頑丈な盾」"
          description="高品質な防具を販売している。"
          onClick={() => onLocationChange('armourShop')}
        />
        
        <LocationButton
          name="ダンジョン入口"
          description="様々なダンジョンへの入口がある。"
          onClick={onDungeonSelect}
          highlight={true}
        />
      </div>
    </div>
  );
};

export default TownSquare;
