// src/components/game/DungeonDisplay.js
import { useNavigate } from 'react-router-dom';
import { useGame } from '../../context/GameContext';
import { getRandomRoomDescription } from '../../utils/game-data';
import { DUNGEONS } from '../../data/dungeons';
import EnemyDisplay from './EnemyDisplay';
import TreasureDisplay from './TreasureDisplay';
import SpecialEventDisplay from './SpecialEventDisplay';
import TrapDisplay from './TrapDisplay'; // 新しいトラップ表示コンポーネントをインポート
import { ACTION_TYPES } from '../../context/actions';

const DungeonDisplay = () => {
  const { state, updateDungeon, log, gainXP, changeTownLocation } = useGame();
  const { dungeon } = state;
  const navigate = useNavigate();
  
  const handleReturnToTown = () => {
    changeTownLocation('square');
    navigate('/town');
  };

  // トラップ解決後の処理
  const handleTrapResolved = (result) => {
    // トラップ解除に成功した場合、少し経験値を与える
    if (result === 'disarmed') {
      const xpGain = 2 + Math.floor(dungeon.level / 2);
      gainXP(xpGain);
      log(`トラップの解除に成功し、${xpGain}の経験値を獲得した！`, 'success');
    }
    
    // 部屋の内容を決定する
    if (result === 'continue') {
      // トラップ表示を消して、通常の部屋内容を表示する
      updateDungeon({ trapPresent: false });
      
      // ルームコンテンツを決定する確率
      const roomType = Math.random();
      
      if (roomType < 0.8) {
        updateDungeon({ treasurePresent: true, treasureOpened: false });
        log(`先に進むと、宝箱を見つけた！`, 'success');
      } else {
        log(`先に進むと、何もない部屋だった。`);
        updateDungeon({ clearedRooms: dungeon.clearedRooms + 1 });
      }
    }
  };
  
  // Helper function to render the appropriate content
  const renderDungeonContent = () => {
    // If there's a special event, render it using our component
    if (state.specialEvent) {
      return <SpecialEventDisplay />;
    }
    
    // トラップがある場合、トラップ表示
    if (dungeon.trapPresent) {
      return <TrapDisplay 
        trap={dungeon.currentTrap} 
        onTrapResolved={handleTrapResolved} 
      />;
    }
    
    // Otherwise render normal room content
    if (dungeon.enemyPresent && dungeon.currentEnemy) {
      return <EnemyDisplay enemy={dungeon.currentEnemy} />;
    } else if (dungeon.treasurePresent) {
      return <TreasureDisplay />;
    } else {
      return <EmptyRoom />;
    }
  };
  
  return (
    <div className="bg-indigo-900 rounded-lg shadow-lg mb-6">
      <h3 className="text-lg font-semibold bg-indigo-800 py-2 px-4 text-sky-300">
        {dungeon.isBossFloor ? (
          <span className="text-red-400">ボス階層 {dungeon.level}</span>
        ) : (
          `ダンジョン階層 ${dungeon.level}`
        )}
      </h3>
      
	  <div className="p-4 min-h-40">
		{renderDungeonContent()}
	  </div>
      
      <div className="px-4 pb-4">
        <div className="flex justify-between text-xs mb-1">
          <span>クリア済み部屋: {dungeon.clearedRooms}</span>
          {!dungeon.isBossFloor && (
            <span>次の階層まで: {dungeon.roomsToNextLevel}</span>
          )}
        </div>
        {!dungeon.isBossFloor && (
          <div className="h-1 bg-indigo-800 rounded overflow-hidden">
            <div 
              className="h-full bg-green-500 transition-all duration-300"
              style={{ 
                width: `${Math.min(100, (dungeon.clearedRooms / dungeon.roomsToNextLevel) * 100)}%` 
              }}
            ></div>
          </div>
        )}
      </div>
    </div>
  );
};

// Empty room component
const EmptyRoom = () => {
  return (
    <div className="text-center py-6">
      <p className="mb-4">{getRandomRoomDescription()}</p>
      <p>先に進むか、休むか選択してください。</p>
    </div>
  );
};

export default DungeonDisplay;
