// src/components/layout/ResponsivePageLayout.js
import React, { useState } from 'react';
import CharacterSheet from '../game/CharacterSheet';
import GameLog from '../game/GameLog';
import QuestStatusBar from '../quests/QuestStatusBar';

/**
 * Responsive page layout that adapts to mobile and desktop screens
 * @param {Object} props Component props
 * @param {string} props.title Page title
 * @param {React.ReactNode} props.actions Action buttons in header
 * @param {React.ReactNode} props.children Main content
 * @param {React.ReactNode} props.sidebar Optional sidebar content that appears below on mobile
 */
const ResponsivePageLayout = ({
                                title,
                                actions = null,
                                children,
                                sidebar = null,
                                showCharacterSheet = true,
                                showGameLog = true
                              }) => {
  const [showSidebar, setShowSidebar] = useState(false);

  // Toggle sidebar visibility on mobile
  const toggleSidebar = () => {
    setShowSidebar(!showSidebar);
  };

  return (
      <div className="max-w-5xl mx-auto">
        {/* Header */}
        <div className="flex flex-col md:flex-row md:justify-between md:items-center mb-4">
          <h2 className="text-xl md:text-2xl font-bold text-sky-400 mb-2 md:mb-0">
            {title}
          </h2>

          <div className="flex items-center space-x-2 self-end">
            {/* Mobile-only sidebar toggle */}
            {sidebar && (
                <button
                    className="md:hidden bg-indigo-700 hover:bg-indigo-600 text-white py-1 px-3 rounded min-w-[60px] text-center"
                    onClick={toggleSidebar}
                >
                  {showSidebar ? 'I' : 'II'}
                </button>
            )}

            {/* Action buttons */}
            {actions && (
                <div className="flex space-x-2">
                  {actions}
                </div>
            )}
          </div>
        </div>

        {/* Quest status */}
        <QuestStatusBar />

        {/* Main content area */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          {/* In mobile: show either sidebar or main content based on toggle */}
          {/* In desktop: show both in correct layout */}

          {/* Sidebar content - character sheet etc. */}
          {(sidebar || showCharacterSheet) && (
              <div
                  className={`md:block md:col-span-1 space-y-4 ${
                      showSidebar ? 'block' : 'hidden md:block'
                  }`}
              >
                {showCharacterSheet && <CharacterSheet />}
                {sidebar}
              </div>
          )}

          {/* Main content area */}
          <div
              className={`md:col-span-2 space-y-4 ${
                  sidebar && showSidebar ? 'hidden md:block' : 'block'
              }`}
          >
            {children}

            {/* Game log */}
            {showGameLog && <GameLog />}
          </div>
        </div>
      </div>
  );
};

export default ResponsivePageLayout;
