// src/utils/uiHelpers.js

/**
 * 親密度レベルをハートで表現するヘルパー関数
 * @param {number} level - 親密度レベル
 * @returns {string} ハート絵文字
 */
export const heartEmoji = (level) => {
  if (level <= 0) return '◇';
  if (level === 1) return '◆';
  if (level === 2) return '◆◆';
  if (level === 3) return '◆◆◆';
  return '◆◆◆◆';
};

/**
 * 難易度を星で表現するヘルパー関数
 * @param {number} level - 難易度レベル(1-5)
 * @returns {string} 星絵文字
 */
export const difficultyStars = (level) => {
  const filledStars = '◆'.repeat(Math.min(5, Math.max(0, level)));
  const emptyStars = '◇'.repeat(Math.max(0, Math.min(5, 5 - level)));
  return filledStars + emptyStars;
};

/**
 * 場所名を取得するヘルパー関数
 * @param {string} location - 場所識別子
 * @returns {string} 表示用の場所名
 */
export const getLocationName = (location) => {
  switch (location) {
    case 'square': return '街の広場';
    case 'tavern': return '酒場「冒険者の休息」';
    case 'weaponShop': return '武器屋「鋼の意志」';
    case 'armourShop': return '防具屋「頑丈な盾」';
    default: return '街';
  }
};

/**
 * 価格表示用ヘルパー関数
 * @param {number} amount - 金額
 * @returns {string} フォーマットされた金額表示
 */
export const formatCurrency = (amount) => {
  return `${amount.toLocaleString()} G`;
};

/**
 * ステータス名を取得するヘルパー関数
 * @param {string} stat - ステータス識別子
 * @returns {string} 表示用のステータス名
 */
export const getStatName = (stat) => {
  switch (stat) {
    case 'str': return '筋力';
    case 'dex': return '敏捷';
    case 'int': return '知力';
    case 'mnd': return '精神';
    default: return stat;
  }
};

/**
 * スキル名を取得するヘルパー関数
 * @param {string} skill - スキル識別子
 * @returns {string} 表示用のスキル名
 */
export const getSkillName = (skill) => {
  switch (skill) {
    case 'sword': return '剣術';
    case 'magic': return '魔法';
    case 'trap': return '罠回避';
    case 'search': return '探索';
    case 'negotiation': return '交渉';
    default: return skill;
  }
};

/**
 * ダイアログタイプに応じたスタイルクラスを取得
 * @param {string} type - ダイアログタイプ ('success', 'error', 'warning', 'info')
 * @returns {Object} 背景色とテキスト色のクラス名
 */
export const getDialogStyles = (type) => {
  switch (type) {
    case 'success':
      return { bg: 'bg-green-800', text: 'text-green-100' };
    case 'error':
      return { bg: 'bg-red-800', text: 'text-red-100' };
    case 'warning':
      return { bg: 'bg-amber-800', text: 'text-amber-100' };
    case 'info':
    default:
      return { bg: 'bg-indigo-800', text: 'text-indigo-100' };
  }
};

/**
 * アイテム名に品質を反映するヘルパー関数
 * @param {string} name - 基本アイテム名
 * @param {string} quality - 品質 ('common', 'uncommon', 'rare', 'epic', 'legendary')
 * @returns {string} 品質を反映したアイテム名
 */
export const getQualityItemName = (name, quality) => {
  switch (quality) {
    case 'uncommon': return `優れた${name}`;
    case 'rare': return `希少な${name}`;
    case 'epic': return `伝説の${name}`;
    case 'legendary': return `神話の${name}`;
    case 'common':
    default:
      return name;
  }
};

/**
 * 品質に応じたテキストカラークラスを取得
 * @param {string} quality - 品質 ('common', 'uncommon', 'rare', 'epic', 'legendary')
 * @returns {string} テキストカラーのTailwindクラス
 */
export const getQualityTextColor = (quality) => {
  switch (quality) {
    case 'uncommon': return 'text-green-300';
    case 'rare': return 'text-blue-300';
    case 'epic': return 'text-purple-300';
    case 'legendary': return 'text-yellow-300';
    case 'common':
    default:
      return 'text-white';
  }
};
