// src/components/quests/QuestRewardDialog.js
import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useGame } from '../../context/GameContext';
import { QUEST_DATABASE } from '../../data/quests';
import { findItemById } from '../../data/itemDatabase';

/**
 * クエスト報酬表示ダイアログ
 * クエスト達成時に表示する報酬ダイアログ
 */
const QuestRewardDialog = ({ questId, onClose }) => {
  const { completeQuest, changeTownLocation, getQuestGiver, getLocationName } = useGame();
  const navigate = useNavigate();
  
  // クエストデータを取得
  const questData = QUEST_DATABASE.find(q => q.id === questId);
  
  if (!questData) {
    return null;
  }
  
  // 依頼者情報を取得
  const questGiver = questData.giver ? getQuestGiver(questData.giver) : null;
  
  // 報酬受け取り処理
  const handleClaimRewards = () => {
    completeQuest(questId);
    onClose();
  };
  
  // 報酬を受け取り、依頼者の場所に移動
  const handleClaimAndGoToGiver = () => {
    completeQuest(questId);
    onClose();
    
    if (questGiver && questGiver.location) {
      // 街のロケーションを変更して移動
      changeTownLocation(questGiver.location);
      navigate('/town');
    }
  };
  
  return (
    <div className="fixed inset-0 bg-black bg-opacity-75 flex items-center justify-center z-50">
      <div className="bg-indigo-900 rounded-lg p-6 max-w-md w-full">
        <div className="text-center mb-4">
          <h2 className="text-2xl font-bold text-yellow-300">クエスト完了！</h2>
          <p className="text-lg text-sky-300 mt-2">{questData.title}</p>
        </div>
        
        {/* 依頼者情報を表示 */}
        {questGiver && (
          <div className="mb-4 p-3 bg-indigo-800 rounded-lg">
            <h3 className="text-sm font-medium mb-2 text-purple-300">依頼者</h3>
            <div>
              <p className="font-semibold text-white">{questGiver.name}</p>
              <p className="text-xs text-indigo-300">{questGiver.title} - {getLocationName(questGiver.location)}</p>
            </div>
          </div>
        )}
        
        <div className="bg-indigo-800 p-4 rounded-lg mb-5">
          <h3 className="text-lg font-semibold mb-3 text-yellow-300">獲得報酬</h3>
          
          <div className="space-y-4">
            {questData.rewards && questData.rewards.map((reward, index) => (
              <div key={index} className="flex items-center">
                {reward.type === 'gold' && (
                  <>
                    <div className="w-10 h-10 bg-yellow-700 rounded-full flex items-center justify-center mr-3">
                      <span className="text-yellow-300 text-lg font-bold">G</span>
                    </div>
                    <div>
                      <p className="font-semibold text-white">{reward.amount} ゴールド</p>
                      <p className="text-xs text-indigo-300">資金が増えました</p>
                    </div>
                  </>
                )}
                
                {reward.type === 'xp' && (
                  <>
                    <div className="w-10 h-10 bg-blue-700 rounded-full flex items-center justify-center mr-3">
                      <span className="text-blue-300 text-lg font-bold">XP</span>
                    </div>
                    <div>
                      <p className="font-semibold text-white">{reward.amount} 経験値</p>
                      <p className="text-xs text-indigo-300">冒険者としての成長</p>
                    </div>
                  </>
                )}
                
                {reward.type === 'item' && (
                  <>
                    <div className="w-10 h-10 bg-purple-700 rounded-full flex items-center justify-center mr-3">
                      <span className="text-purple-300 text-lg font-bold">★</span>
                    </div>
                    <div>
                      {(() => {
                        const item = findItemById(reward.id);
                        return (
                          <>
                            <p className="font-semibold text-white">
                              {item?.name || reward.id}
                            </p>
                            <p className="text-xs text-indigo-300">
                              {item?.description || '説明文がありません。'}
                            </p>
                          </>
                        );
                      })()}
                    </div>
                  </>
                )}
              </div>
            ))}
          </div>
        </div>
        
        <div className="flex flex-col space-y-2">
          <button
            onClick={handleClaimRewards}
            className="px-6 py-3 bg-yellow-600 hover:bg-yellow-700 text-white font-bold rounded-lg transition-colors"
          >
            報酬を受け取る
          </button>
          
          {/* 依頼者がいる場合は依頼者の場所に戻るオプションを表示 */}
          {questGiver && (
            <button
              onClick={handleClaimAndGoToGiver}
              className="px-6 py-3 bg-purple-600 hover:bg-purple-700 text-white font-bold rounded-lg transition-colors"
            >
              報酬を受け取り、{questGiver.name}に報告する
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

export default QuestRewardDialog;
