// src/components/town/LocationButton.js
import React from 'react';

const LocationButton = ({ name, description, icon, onClick, highlight = false }) => {
  return (
    <button
      onClick={onClick}
      className={`${
        highlight ? 'bg-indigo-700 hover:bg-indigo-600' : 'bg-indigo-800 hover:bg-indigo-700'
      } rounded-lg p-4 text-left transition-colors flex gap-4 items-center`}
    >
      <div>
        <h4 className={`font-semibold ${highlight ? 'text-amber-300' : 'text-sky-300'}`}>{name}</h4>
        <p className="text-sm text-indigo-200">{description}</p>
      </div>
    </button>
  );
};

export default LocationButton;
