// src/components/town/ArmourShop.js
import React, { useState, useEffect } from 'react';
import { useGame } from '../../context/GameContext';
import ConversationPanel from './ConversationPanel';

const ArmourShop = ({ onBack }) => {
  const { state, generateShopInventory, buyItem } = useGame();
  const [showArmours, setShowArmours] = useState(false);
  const [showTalk, setShowTalk] = useState(false);
  const [selectedArmour, setSelectedArmour] = useState(null);
  
  // 初回表示時に在庫を生成
  useEffect(() => {
    if (state.town.shopInventory.armours.length === 0) {
      generateShopInventory('armours', state.character.level);
    }
  }, []);
  
  // 店の在庫
  const armours = state.town.shopInventory.armours || [];
  
  // プレイヤーの所持金
  const gold = state.character.gold;
  
  return (
    <div className="bg-indigo-900 rounded-lg shadow-lg p-4">
      <h3 className="text-xl font-semibold mb-4 text-sky-300">防具屋「頑丈な盾」</h3>
      
      {!showArmours && !showTalk ? (
        <>
          <p className="mb-6">
            さまざまな防具が整然と並べられた店。明るい日差しが窓から差し込み、金属の防具が輝いている。
            ベテラン冒険者のような雰囲気の女性店主があなたを迎え入れる。
          </p>
          
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 mb-6">
            <button
              onClick={() => setShowArmours(true)}
              className="bg-indigo-800 hover:bg-indigo-700 rounded-lg p-4 text-left transition-colors"
            >
              <h4 className="font-semibold text-sky-300">防具を見る</h4>
              <p className="text-sm text-indigo-200">販売中の防具を確認する。</p>
            </button>
            
            <button
              onClick={() => setShowTalk(true)}
              className="bg-indigo-800 hover:bg-indigo-700 rounded-lg p-4 text-left transition-colors"
            >
              <h4 className="font-semibold text-sky-300">店主と話す</h4>
              <p className="text-sm text-indigo-200">経験豊富な女性店主から防具の知識を教わる。</p>
            </button>
          </div>
        </>
      ) : showArmours ? (
        <div className="armour-list">
          <h4 className="font-semibold text-lg mb-4 text-amber-300">販売中の防具</h4>
          
          {selectedArmour ? (
            <div className="armour-detail mb-6">
              <div className="bg-indigo-800 p-4 rounded-lg mb-4">
                <h5 className="font-medium text-sky-200 mb-2">{selectedArmour.name}</h5>
                <div className="grid grid-cols-2 gap-2 mb-3">
                  <div className="text-sm">防御力: <span className="text-green-300">+{selectedArmour.defense}</span></div>
                  <div className="text-sm">重さ: <span className="text-yellow-300">{selectedArmour.weight}</span></div>
                </div>
                <p className="text-indigo-200 text-sm mb-3">{selectedArmour.description}</p>
                <div className="flex justify-between items-center">
                  <span className="text-amber-300">{selectedArmour.price} G</span>
                  <button
                    onClick={() => buyItem(selectedArmour)}
                    disabled={gold < selectedArmour.price}
                    className={`py-1 px-3 rounded ${
                      gold >= selectedArmour.price
                        ? 'bg-green-600 hover:bg-green-700 text-white'
                        : 'bg-gray-600 text-gray-300 cursor-not-allowed'
                    }`}
                  >
                    購入する
                  </button>
                </div>
              </div>
              
              <button
                onClick={() => setSelectedArmour(null)}
                className="bg-indigo-700 hover:bg-indigo-600 text-white py-1 px-3 rounded-lg transition-colors"
              >
                他の防具を見る
              </button>
            </div>
          ) : (
            <>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-6">
                {armours.map(armour => (
                  <div
                    key={armour.id}
                    onClick={() => setSelectedArmour(armour)}
                    className="bg-indigo-800 hover:bg-indigo-700 p-3 rounded-lg cursor-pointer transition-colors"
                  >
                    <div className="flex justify-between items-center mb-1">
                      <h5 className="font-medium text-sky-200">{armour.name}</h5>
                      <span className="text-amber-300">{armour.price} G</span>
                    </div>
                    <div className="text-xs text-indigo-300">防御力: +{armour.defense}</div>
                  </div>
                ))}
              </div>
              
              <div className="mb-4 bg-indigo-800 p-2 rounded">
                <span className="text-sm">所持金: <span className="text-amber-300">{gold} G</span></span>
              </div>
            </>
          )}
          
          <button
            onClick={() => setShowArmours(false)}
            className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded-lg transition-colors"
          >
            戻る
          </button>
        </div>
      ) : showTalk ? (
        <ConversationPanel
          characterId="armourShopGirl"
          onExitConversation={() => setShowTalk(false)}
        />
      ) : null}
      
      {/* 戻るボタン */}
      {!showArmours && !showTalk && (
        <button
          onClick={onBack}
          className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded-lg transition-colors"
        >
          広場へ戻る
        </button>
      )}
    </div>
  );
};

export default ArmourShop;
