// src/context/reducers/index.js
import { ACTION_TYPES } from '../actions';
import initialGameState from '../initialState';
import characterReducer from './characterReducer';
import dungeonReducer from './dungeonReducer';
import townReducer from './townReducer';
import logReducer from './logReducer';
import eventReducer from './eventReducer';
import uiReducer from './uiReducer';
import equipmentReducer from './equipmentReducer';
import questReducer from './questReducer';
import { QUEST_DATABASE } from '../../data/quests';

// 前提条件のないクエストを取得
const getInitialQuests = () => {
  return QUEST_DATABASE.filter(quest => 
    !quest.requiredQuestIds || quest.requiredQuestIds.length === 0
  );
};

// 初期クエストを生成
const prepareInitialQuests = () => {
  const initialQuests = getInitialQuests();
  
  return initialQuests.map(quest => {
    const objectives = quest.objectives.map(obj => ({
      id: obj.id,
      isCompleted: false,
      currentCount: obj.count ? 0 : undefined,
      maxCount: obj.count
    }));
    
    return {
      id: quest.id,
      startedAt: new Date().toISOString(),
      objectives
    };
  });
};

/**
 * ルートリデューサー - すべてのリデューサーを結合
 * @param {Object} state - 現在のゲーム状態
 * @param {Object} action - 実行するアクション
 * @returns {Object} 更新されたゲーム状態
 */
export const gameReducer = (state, action) => {
  // 特殊ケース: ゲームリセット
  if (action.type === ACTION_TYPES.RESET_GAME) {
    console.log("RESET_GAME action received with payload:", action.payload);
    
    // 前提条件のないクエストを取得
    const initialActiveQuests = prepareInitialQuests();
    console.log("Setting initial quests on reset:", initialActiveQuests);
    
    return {
      ...initialGameState,
      questJournal: {
        ...initialGameState.questJournal,
        activeQuests: initialActiveQuests,
      },
      hasCreatedCharacter: action.payload.hasCreatedCharacter || false,
      gameLog: [{ text: "ゲーム開始！ダンジョンの探索を始めましょう。", className: '' }]
    };
  }
  
  // 特殊ケース: セーブデータのロード
  if (action.type === ACTION_TYPES.LOAD_SAVED_GAME) {
    return {
      ...state,
      character: action.payload.character,
      dungeon: action.payload.dungeon,
      town: action.payload.town || initialGameState.town,
      questJournal: action.payload.questJournal || initialGameState.questJournal,
      isGameOver: action.payload.isGameOver || false,
      hasCreatedCharacter: action.payload.hasCreatedCharacter || false,
      gameLog: [
        ...state.gameLog,
        { text: "保存したゲームをロードしました。", className: 'success' }
      ]
    };
  }
  
  // クエスト関連のアクションを先に処理
  if (
    action.type === ACTION_TYPES.SET_INITIAL_QUESTS ||
    action.type === ACTION_TYPES.START_QUEST ||
    action.type === ACTION_TYPES.COMPLETE_QUEST ||
    action.type === ACTION_TYPES.FAIL_QUEST ||
    action.type === ACTION_TYPES.TRACK_QUEST ||
    action.type === ACTION_TYPES.UPDATE_QUEST_OBJECTIVE ||
    action.type === ACTION_TYPES.UPDATE_QUEST_OBJECTIVE_COUNT
  ) {
    // questReducerに委譲
    const updatedState = questReducer(state, action);
    
    // questJournalが更新されたか確認
    if (updatedState.questJournal !== state.questJournal) {
      console.log("Quest state updated:", updatedState.questJournal);
      // questJournalが更新されていれば、更新された状態を返す
      return {
        ...state,
        questJournal: updatedState.questJournal
      };
    }
  }
  
  // 各ドメイン固有のリデューサーに委譲
  const uiState = uiReducer(
    {
      isGameOver: state.isGameOver,
      isLevelingUp: state.isLevelingUp,
      hasCreatedCharacter: state.hasCreatedCharacter,
      ui: state.ui || initialGameState.ui
    }, 
    action
  );
  
  // 装備関連アクションはcharacterReducerの前に処理
  // equipmentReducerが処理するアクションかどうかを判定
  const isEquipmentAction = [
    ACTION_TYPES.EQUIP_ITEM,
    ACTION_TYPES.UNEQUIP_ITEM,
    ACTION_TYPES.ADD_TO_INVENTORY,
    ACTION_TYPES.REMOVE_FROM_INVENTORY,
    ACTION_TYPES.BUY_ITEM,
    ACTION_TYPES.SELL_ITEM
  ].includes(action.type);
  
  // 装備関連アクションの場合、equipmentReducerで処理
  const updatedCharacter = isEquipmentAction
    ? equipmentReducer(state.character, action)
    : characterReducer(state.character, action);
  
  return {
    ...state,
    character: updatedCharacter,
    dungeon: dungeonReducer(state.dungeon, action),
    town: townReducer(state.town, action),
    gameLog: logReducer(state.gameLog, action),
    specialEvent: eventReducer(state.specialEvent, action),
    // UIステート
    isGameOver: uiState.isGameOver,
    isLevelingUp: uiState.isLevelingUp,
    hasCreatedCharacter: uiState.hasCreatedCharacter,
    ui: uiState.ui
  };
};

export default gameReducer;
