// src/context/reducers/questReducer.js
import { ACTION_TYPES } from '../actions';
import { QUEST_DATABASE } from '../../data/quests';

/**
 * クエストジャーナル関連のリデューサー
 * @param {Object} state - 現在のゲーム状態全体
 * @param {Object} action - 実行されるアクション
 * @returns {Object} 更新されたゲーム状態
 */
export const questReducer = (state, action) => {
  // 初期状態のquestJournalを確保
  if (!state.questJournal) {
    state = {
      ...state,
      questJournal: {
        activeQuests: [],
        completedQuests: [],
        failedQuests: [],
        trackedQuestId: null
      }
    };
  }

  switch (action.type) {
    case ACTION_TYPES.SET_INITIAL_QUESTS: {
      console.log("questReducer - SET_INITIAL_QUESTS:", action.payload);
      
      // 初期クエストの配列を受け取り、questJournalに設定
      const initialQuests = action.payload;
      if (!initialQuests || !Array.isArray(initialQuests) || initialQuests.length === 0) {
        return state;
      }
      
      // 各クエストのobjectivesを初期化
      const preparedQuests = initialQuests.map(quest => {
        const objectives = quest.objectives.map(obj => ({
		  id: obj.id,
          isCompleted: false,
          currentCount: obj.count ? 0 : undefined,
          maxCount: obj.count
        }));
        
        return {
          id: quest.id,
          startedAt: new Date().toISOString(),
          objectives
        };
      });
      
      console.log("questReducer - Setting initial quests:", preparedQuests);
      
      // 新しいquestJournalを返す
      return {
        ...state,
        questJournal: {
          ...state.questJournal,
          activeQuests: [...preparedQuests]
        }
      };
    }
    
    case ACTION_TYPES.START_QUEST: {
      const questId = action.payload;
      
      console.log("questReducer - START_QUEST:", questId);
      
      // 既に開始済み、完了済み、失敗済みのクエストは対象外
      if (
        state.questJournal?.activeQuests?.some(q => q.id === questId) ||
        state.questJournal?.completedQuests?.includes(questId) ||
        state.questJournal?.failedQuests?.includes(questId)
      ) {
        return state;
      }
      
      // クエストデータベースからクエスト情報を取得
      const questData = QUEST_DATABASE.find(q => q.id === questId);
      if (!questData) return state;
      
      // 目標情報を初期化
      const objectives = questData.objectives.map(obj => ({
        id: obj.id,
        isCompleted: false,
        currentCount: obj.count ? 0 : undefined,
        maxCount: obj.count
      }));
      
      console.log("questReducer - Starting quest:", {
        id: questId,
        objectives
      });
      
      return {
        ...state,
        questJournal: {
          ...state.questJournal,
          activeQuests: [
            ...(state.questJournal?.activeQuests || []),
            {
              id: questId,
              startedAt: new Date().toISOString(),
              objectives
            }
          ]
        }
      };
    }
    
    case ACTION_TYPES.COMPLETE_QUEST: {
      const questId = action.payload;
      
      // クエストが進行中かどうか確認
      if (!state.questJournal?.activeQuests?.some(q => q.id === questId)) {
        return state;
      }
      
      // クエストを進行中リストから削除し、完了リストに追加
      return {
        ...state,
        questJournal: {
          ...state.questJournal,
          activeQuests: state.questJournal.activeQuests.filter(q => q.id !== questId),
          completedQuests: [...(state.questJournal?.completedQuests || []), questId],
          // 追跡中のクエストなら追跡解除
          trackedQuestId: state.questJournal?.trackedQuestId === questId ? null : state.questJournal?.trackedQuestId
        }
      };
    }
    
    case ACTION_TYPES.FAIL_QUEST: {
      const questId = action.payload;
      
      // クエストが進行中かどうか確認
      if (!state.questJournal?.activeQuests?.some(q => q.id === questId)) {
        return state;
      }
      
      // クエストを進行中リストから削除し、失敗リストに追加
      return {
        ...state,
        questJournal: {
          ...state.questJournal,
          activeQuests: state.questJournal.activeQuests.filter(q => q.id !== questId),
          failedQuests: [...(state.questJournal?.failedQuests || []), questId],
          // 追跡中のクエストなら追跡解除
          trackedQuestId: state.questJournal?.trackedQuestId === questId ? null : state.questJournal?.trackedQuestId
        }
      };
    }
    
    case ACTION_TYPES.TRACK_QUEST: {
      const questId = action.payload;
      
      // クエストが進行中かnullかどうか確認（nullの場合は追跡解除）
      if (questId === null || state.questJournal?.activeQuests?.some(q => q.id === questId)) {
        return {
          ...state,
          questJournal: {
            ...state.questJournal,
            trackedQuestId: questId
          }
        };
      }
      
      return state;
    }
    
    case ACTION_TYPES.UPDATE_QUEST_OBJECTIVE: {
      const { questId, objectiveId, isCompleted } = action.payload;
      
      // クエストとその目標が存在するか確認
      const questIndex = state.questJournal?.activeQuests?.findIndex(q => q.id === questId);
      if (questIndex === -1 || questIndex === undefined) return state;
      
      const quest = state.questJournal.activeQuests[questIndex];
      const objectiveIndex = quest.objectives.findIndex(o => o.id === objectiveId);
      if (objectiveIndex === -1) return state;
      
      // 目標を更新
      const updatedObjectives = [...quest.objectives];
      updatedObjectives[objectiveIndex] = {
        ...updatedObjectives[objectiveIndex],
        isCompleted
      };
      
      // 更新されたクエストリスト
      const updatedActiveQuests = [...state.questJournal.activeQuests];
      updatedActiveQuests[questIndex] = {
        ...quest,
        objectives: updatedObjectives
      };
      
      return {
        ...state,
        questJournal: {
          ...state.questJournal,
          activeQuests: updatedActiveQuests
        }
      };
    }
    
    case ACTION_TYPES.UPDATE_QUEST_OBJECTIVE_COUNT: {
      const { questId, objectiveId, count } = action.payload;
      
      // クエストとその目標が存在するか確認
      const questIndex = state.questJournal?.activeQuests?.findIndex(q => q.id === questId);
      if (questIndex === -1 || questIndex === undefined) return state;
      
      const quest = state.questJournal.activeQuests[questIndex];
      const objectiveIndex = quest.objectives.findIndex(o => o.id === objectiveId);
      if (objectiveIndex === -1) return state;
      
      const objective = quest.objectives[objectiveIndex];
      
      // カウンターがない目標は対象外
      if (objective.currentCount === undefined || objective.maxCount === undefined) {
        return state;
      }
      
      // 既に完了している場合は更新しない
      if (objective.isCompleted) return state;
      
      // カウンターを増加
      const newCount = Math.min(objective.maxCount, count);
      
      // 目標を更新
      const updatedObjectives = [...quest.objectives];
      updatedObjectives[objectiveIndex] = {
        ...objective,
        currentCount: count
      };
      
      // 更新されたクエストリスト
      const updatedActiveQuests = [...state.questJournal.activeQuests];
      updatedActiveQuests[questIndex] = {
        ...quest,
        objectives: updatedObjectives
      };
      
      return {
        ...state,
        questJournal: {
          ...state.questJournal,
          activeQuests: updatedActiveQuests
        }
      };
    }
    
    default:
      return state;
  }
};

export default questReducer;
