// src/context/utils/questUtils.js
import { ACTION_TYPES } from '../actions';
import { QUEST_DATABASE } from '../../data/quests';
import { findItemById } from "../../data/itemDatabase";

/**
 * クエスト関連のユーティリティ関数を作成
 * @param {Object} state - 現在のゲーム状態
 * @param {Function} dispatch - リデューサーのディスパッチ関数
 * @param {Object} utils - ユーティリティ関数
 * @returns {Object} クエスト関連のユーティリティ関数
 */
const createQuestUtils = (state, dispatch, utils) => {
  const { log } = utils;

  // 先に questUtils オブジェクトを宣言
  const questUtils = {};

  // 宣言的な条件を評価するヘルパー関数
  questUtils.evaluateCondition = (condition) => {
    // 関数の場合は直接実行（後方互換性のため）
    if (typeof condition === 'function') {
      return condition(state);
    }
    
    // オブジェクトでない場合は常にtrue
    if (!condition || typeof condition !== 'object') {
      return true;
    }
    
    // 演算子に基づいて値を比較する共通関数
    const compareValues = (actual, expected, operator = 'gte') => {
      switch (operator) {
        case 'eq': return actual === expected;
        case 'neq': return actual !== expected;
        case 'gt': return actual > expected;
        case 'gte': return actual >= expected;
        case 'lt': return actual < expected;
        case 'lte': return actual <= expected;
        default: return actual >= expected; // デフォルトはgte
      }
    };
    
    // 条件タイプに基づいて評価
    switch (condition.type) {
      case 'conversation_level': {
        // 会話レベルチェック
        const { characterId, level, operator } = condition;
        const conversationData = state.town.conversations[characterId] || { level: 0 };
        const currentLevel = conversationData.level || 0;
        
        return compareValues(currentLevel, level, operator);
      }
      
      case 'character_level': {
        // キャラクターレベルチェック
        const { level, operator } = condition;
        const characterLevel = state.character.level || 1;
        
        return compareValues(characterLevel, level, operator);
      }
      
      case 'has_item': {
        // アイテム所持チェック
        const { itemId, category } = condition;
        
        if (category) {
          // カテゴリ指定がある場合
          return state.character.inventory[category]?.some(item => item.id === itemId) || false;
        } else {
          // 全カテゴリ検索
          return Object.values(state.character.inventory).some(items => 
            items.some(item => item.id === itemId)
          ) || false;
        }
      }
      
      case 'gold_amount': {
        // 所持金チェック
        const { amount, operator } = condition;
        const gold = state.character.gold || 0;
        
        return compareValues(gold, amount, operator);
      }
      
      case 'completed_dungeons': {
        // ダンジョン攻略数チェック
        const { count, operator } = condition;
        const completedCount = state.character?.completedDungeons?.length || 0;
        
        return compareValues(completedCount, count, operator);
      }
      
      case 'quest_status': {
        // クエスト状態チェック
        const { questId, status } = condition;
        
        if (status === 'completed') {
          return state.questJournal?.completedQuests?.includes(questId) || false;
        } else if (status === 'active') {
          return state.questJournal?.activeQuests?.some(q => q.id === questId) || false;
        } else if (status === 'failed') {
          return state.questJournal?.failedQuests?.includes(questId) || false;
        }
        return false;
      }
      
      case 'and': {
        // AND条件（すべての条件を満たす）
        return condition.conditions.every(subCondition => 
          questUtils.evaluateCondition(subCondition)
        );
      }
      
      case 'or': {
        // OR条件（いずれかの条件を満たす）
        return condition.conditions.some(subCondition => 
          questUtils.evaluateCondition(subCondition)
        );
      }
      
      case 'not': {
        // NOT条件（条件を満たさない）
        return !questUtils.evaluateCondition(condition.condition);
      }
      
      default:
        console.warn(`未知の条件タイプ: ${condition.type}`);
        return false;
    }
  };

  // questUtils オブジェクトにメソッドを追加
  questUtils.getAvailableQuests = () => {
      return QUEST_DATABASE.filter(quest => {
        // すでに開始、完了、失敗したクエストは除外
        if (
          state.questJournal?.activeQuests?.some(q => q.id === quest.id) ||
          state.questJournal?.completedQuests?.includes(quest.id) ||
          state.questJournal?.failedQuests?.includes(quest.id)
        ) {
          return false;
        }
        
        // レベル要件をチェック
        if (quest.requiredLevel && state.character.level < quest.requiredLevel) {
          return false;
        }
        
        // 前提クエストをチェック
        if (quest.requiredQuestIds && quest.requiredQuestIds.length > 0) {
          const allRequiredCompleted = quest.requiredQuestIds.every(reqId => 
            state.questJournal?.completedQuests?.includes(reqId)
          );
          if (!allRequiredCompleted) return false;
        }
        
        // カスタム解放条件をチェック
        if (quest.unlockCondition) {
          return questUtils.evaluateCondition(quest.unlockCondition);
        }
        
        return true;
      });
  };
  
  questUtils.startQuest = (questId) => {
      const questData = QUEST_DATABASE.find(q => q.id === questId);
      if (!questData) return false;
      
      // すでに開始、完了、失敗済みならスキップ
      if (
        state.questJournal?.activeQuests?.some(q => q.id === questId) ||
        state.questJournal?.completedQuests?.includes(questId) ||
        state.questJournal?.failedQuests?.includes(questId)
      ) {
        return false;
      }
      
      dispatch({
        type: ACTION_TYPES.START_QUEST,
        payload: questId
      });
      
      log(`新しいクエスト ${questData.title}を受注しました。`, 'success');
      return true;
  };
  
  questUtils.completeQuest = (questId) => {
    const questData = QUEST_DATABASE.find(q => q.id === questId);
    if (!questData) return false;
    
    // アクティブなクエストでなければ何もしない
    const activeQuest = state.questJournal?.activeQuests?.find(q => q.id === questId);
    if (!activeQuest) return false;
    
    // 全ての目標が完了しているか確認
    const allObjectivesComplete = activeQuest.objectives.every(obj => obj.isCompleted);
    if (!allObjectivesComplete) {
      log(`クエスト ${questData.title}はまだ全ての目標が完了していません。`, 'failure');
      return false;
    }
    
    dispatch({
      type: ACTION_TYPES.COMPLETE_QUEST,
      payload: questId
    });
    
    // 報酬を与える
    if (questData.rewards) {
      questData.rewards.forEach(reward => {
        switch (reward.type) {
          case 'gold':
            dispatch({
              type: ACTION_TYPES.UPDATE_CHARACTER,
              payload: { gold: state.character.gold + reward.amount }
            });
            log(`報酬として ${reward.amount}ゴールドを獲得！`, 'success');
            break;
          case 'xp':
            // 経験値を付与
            if (utils.gainXP) {
              utils.gainXP(reward.amount);
            } else {
              dispatch({
                type: ACTION_TYPES.UPDATE_CHARACTER,
                payload: { xp: state.character.xp + reward.amount }
              });
              log(`報酬として ${reward.amount}経験値を獲得！`, 'success');
            }
            break;
          case 'item': {
            const item = findItemById(reward.id);
            if (item) {
              dispatch({
                type: ACTION_TYPES.ADD_TO_INVENTORY,
                payload: item
              });
              log(`報酬として ${item.name}を獲得！`, 'success');
            }
            break;
          }
          default:
            break;
        }
      });
    }
    
    // 次のクエストを解放
    if (questData.nextQuests && questData.nextQuests.length > 0) {
      questData.nextQuests.forEach(nextQuestId => {
        const nextQuest = QUEST_DATABASE.find(q => q.id === nextQuestId);
        if (nextQuest) {
          log(`クエスト ${nextQuest.title}が解放されました。`, 'info');
        }
      });
    }
    
    log(`クエスト ${questData.title}を完了しました！`, 'critical');
    return true;
  };
  
  questUtils.failQuest = (questId) => {
    const questData = QUEST_DATABASE.find(q => q.id === questId);
    if (!questData) return false;
    
    dispatch({
      type: ACTION_TYPES.FAIL_QUEST,
      payload: questId
    });
    
    log(`クエスト ${questData.title}に失敗しました。`, 'failure');
    return true;
  };
  
  questUtils.trackQuest = (questId) => {
    dispatch({
      type: ACTION_TYPES.TRACK_QUEST,
      payload: questId
    });
    
    if (questId) {
      const questData = QUEST_DATABASE.find(q => q.id === questId);
      if (questData) {
        log(`クエスト ${questData.title}を追跡します。`, 'info');
      }
    } else {
      log(`クエストの追跡を解除しました。`, 'info');
    }
  };
  
  questUtils.updateQuestObjectivesByTrigger = (triggerType, data = {}) => {
    if (!state.questJournal || !state.questJournal.activeQuests) return;

    state.questJournal.activeQuests.forEach(quest => {
      const questData = QUEST_DATABASE.find(q => q.id === quest.id);
      if (!questData) return;
      
      quest.objectives.forEach((objective, index) => {
        // 既に完了している目標はスキップ
        if (objective.isCompleted) return;
        
        const objData = questData.objectives[index];
        
        // トリガータイプが一致するか確認
        if (objData.triggerType !== triggerType) return;

        let shouldComplete = true;
        
        // 特定のトリガータイプに対する追加条件を確認
        switch (triggerType) {
          case 'dungeon_explore':
            // 特定のダンジョンタイプが指定されている場合
            if (objData.targetDungeon && objData.targetDungeon !== data.dungeonType) {
              shouldComplete = false;
            }
            break;
            
          case 'defeat_monster':
            // 特定のダンジョンタイプが指定されている場合
            if (objData.targetDungeon && objData.targetDungeon !== data.dungeonType) {
              shouldComplete = false;
            }
            // 特定のモンスターが指定されている場合
            else if (objData.targetMonster && objData.targetMonster !== data.monsterName) {
              shouldComplete = false;
            }
            // カウント型の目標の場合
            else if (objData.count) {
              const currentCount = objective.currentCount || 0;
              const newCount = currentCount + 1;
              
              // カウントを更新
              dispatch({
                type: ACTION_TYPES.UPDATE_QUEST_OBJECTIVE_COUNT,
                payload: { 
                  questId: quest.id, 
                  objectiveId: objective.id, 
                  count: newCount
                }
              });
              
              // 目標達成したかチェック
              if (newCount >= objData.count) {
              } else {
                shouldComplete = false;
              }
              log(`クエスト ${questData.title}: ${objData.targetMonster}を倒した！ (${newCount}/${objData.count})`, 'info');
            }
            break;
            
          case 'defeat_boss':
            // 特定のダンジョンが指定されている場合
            if (objData.targetDungeon && objData.targetDungeon !== data.dungeonType) {
              shouldComplete = false;
            }
            // 特定のボスが指定されている場合
            else if (objData.targetBoss && objData.targetBoss !== data.monsterName) {
              shouldComplete = false;
            }
            log(`クエスト ${questData.title}: ${objData.targetBoss}を倒した！`, 'info');
            break;
            
          case 'talk_to_npc':
            // キャラクターが指定されていない場合は完了しない
            if (!objData.targetCharacterId) {
              shouldComplete = false;
            }
            // 特定のキャラクターが指定されている場合
            else if (objData.targetCharacterId !== data.characterId) {
              shouldComplete = false;
            }
            // 特定の会話が指定されている場合（特定の会話トピックが必要な場合）
            else if (objData.requiredConversationId && 
                objData.requiredConversationId !== data.conversationId) {
              shouldComplete = false;
            }
            // 特定のうわさが指定されている場合
            else if (objData.requiredRumorId) {
              // うわさが発見されたかチェック
              const rumorDiscovered = state.character.discoveredRumors && 
                                      state.character.discoveredRumors.includes(objData.requiredRumorId);
              
              // データに特定のうわさIDが含まれているかチェック
              const rumorInData = data.rumorId === objData.requiredRumorId;
              
              // どちらかの条件を満たしていない場合は完了しない
              if (!rumorDiscovered && !rumorInData) {
                shouldComplete = false;
              }
            }
            break;
            
          case 'collect_item':
            // 特定のアイテムが指定されている場合
            if (objData.targetItem && objData.targetItem !== data.itemId) {
              shouldComplete = false;
            }
            // カウント型の目標の場合
            else if (objData.count) {
              const currentCount = objective.currentCount || 0;
              const newCount = currentCount + 1;
              
              // カウントを更新
              dispatch({
                type: ACTION_TYPES.UPDATE_QUEST_OBJECTIVE_COUNT,
                payload: { 
                  questId: quest.id, 
                  objectiveId: objective.id, 
                  count: newCount
                }
              });
              
              // 目標達成したかチェック
              if (newCount >= objData.count) {
              } else {
                shouldComplete = false;
              }
              log(`クエスト ${questData.title}: ${objData.targetItem}を入手しました (${newCount}/${objData.count})`, 'info');
            }
            break;
            
          case 'dungeon_level_up':
            // 特定のレベルが指定されている場合
            if (objData.targetLevel && objData.targetLevel !== data.level) {
              shouldComplete = false;
            }
            break;
            
          case 'open_treasure':
            // 特定の宝箱タイプが指定されている場合
            if (objData.treasureType && objData.treasureType !== data.treasureType) {
              shouldComplete = false;
            }
            break;
            
          case 'reach_location':
            // 特定の場所が指定されている場合
            if (objData.targetLocation && objData.targetLocation !== data.locationId) {
              shouldComplete = false;
            }
            break;
            
          case 'complete_dungeon':
            // 特定のダンジョンタイプが指定されている場合
            if (objData.targetDungeon && objData.targetDungeon !== data.dungeonType) {
              shouldComplete = false;
            }
            break;

          default:
            console.warn(`未知のトリガータイプ: ${triggerType}`);
            break;
        }
        
        // 目標を完了としてマーク
        if (shouldComplete) {
          dispatch({
            type: ACTION_TYPES.UPDATE_QUEST_OBJECTIVE,
            payload: { 
              questId: quest.id, 
              objectiveId: objective.id, 
              isCompleted: true 
            }
          });
          
          log(`クエスト ${questData.title}: ${objData.description}を完了しました！`, 'success');
        }
      });
      
      // クエスト全体の完了チェック
      const allObjectivesCompleted = quest.objectives.every(obj => obj.isCompleted);
      if (allObjectivesCompleted && !quest.isCompleted) {
        dispatch({
          type: ACTION_TYPES.COMPLETE_QUEST,
          payload: { questId: quest.id }
        });
        
        log(`クエスト ${questData.title}をコンプリートしました！`, 'critical');
        
        // 報酬の付与
        if (questData.rewards) {
          questData.rewards.forEach(reward => {
            switch (reward.type) {
              case 'gold':
                dispatch({
                  type: ACTION_TYPES.UPDATE_CHARACTER,
                  payload: { gold: state.character.gold + reward.amount }
                });
                log(`報酬: ${reward.amount} ゴールドを獲得しました！`, 'success');
                break;
                
              case 'xp':
                if (utils.gainXP) {
                  utils.gainXP(reward.amount);
                  log(`報酬: ${reward.amount} 経験値を獲得しました！`, 'success');
                }
                break;
                
              case 'item':
                // アイテムデータベースからアイテムを検索
                let item = null;
                for (const category of ['weapons', 'armours', 'accessories', 'consumables']) {
                  if (state.itemDatabase && state.itemDatabase[category]) {
                    const found = state.itemDatabase[category].find(i => i.id === reward.id);
                    if (found) {
                      item = found;
                      break;
                    }
                  }
                }
                
                if (item) {
                  dispatch({
                    type: ACTION_TYPES.ADD_TO_INVENTORY,
                    payload: item
                  });
                  log(`報酬: ${item.name}を獲得しました！`, 'success');
                }
                break;

              default:
                console.warn(`未知の報酬タイプ: ${reward.type}`);
                break;
            }
          });
        }
        
        // 次のクエストの解放
        if (questData.nextQuests && questData.nextQuests.length > 0) {
          questData.nextQuests.forEach(nextQuestId => {
            const nextQuest = QUEST_DATABASE.find(q => q.id === nextQuestId);
            if (nextQuest) {
              // レベル要件を満たしているか確認
              if (!nextQuest.requiredLevel || state.character.level >= nextQuest.requiredLevel) {
                // 前提クエストをすべて完了しているか確認
                const allRequiredQuestsCompleted = !nextQuest.requiredQuestIds || 
                  nextQuest.requiredQuestIds.every(reqId => 
                    state.questJournal.completedQuests.some(cq => cq.id === reqId)
                  );
                
                if (allRequiredQuestsCompleted) {
                  // 追加の解放条件をチェック
                  const unlockConditionMet = !nextQuest.unlockCondition || 
                    questUtils.evaluateCondition(nextQuest.unlockCondition);
                  
                  if (unlockConditionMet) {
                    // クエストを追加
                    const newQuestProgress = {
                      id: nextQuest.id,
                      title: nextQuest.title,
                      objectives: nextQuest.objectives.map(obj => ({
                        id: obj.id,
                        isCompleted: false,
                        currentCount: obj.count ? 0 : undefined
                      })),
                      isCompleted: false,
                      dateStarted: new Date().toISOString()
                    };
                    
                    dispatch({
                      type: ACTION_TYPES.ADD_QUEST,
                      payload: newQuestProgress
                    });
                    
                    log(`クエスト ${nextQuest.title}が解放されました。`, 'critical');
                  }
                }
              }
            }
          });
        }
      }
    });
  };
  
  questUtils.getTrackedQuest = () => {
    const trackedQuestId = state.questJournal?.trackedQuestId;
    if (!trackedQuestId) return null;
    
    const questData = QUEST_DATABASE.find(q => q.id === trackedQuestId);
    const activeQuest = state.questJournal?.activeQuests?.find(q => q.id === trackedQuestId);
    
    if (!questData || !activeQuest) return null;
    
    return {
      id: trackedQuestId,
      title: questData.title,
      description: questData.description,
      type: questData.type,
      objectives: activeQuest.objectives.map(obj => {
        const objData = questData.objectives.find(o => o.id === obj.id);
        return {
          id: obj.id,
          description: objData?.description || '',
          isCompleted: obj.isCompleted,
          currentCount: obj.currentCount,
          maxCount: obj.maxCount
        };
      }),
      // 完了した目標の数を計算
      completedObjectives: activeQuest.objectives.filter(obj => obj.isCompleted).length,
      totalObjectives: activeQuest.objectives.length
    };
  };
  
  questUtils.getQuestsFromGiver = (giverId) => {
    return QUEST_DATABASE.filter(quest => {
      // 依頼者が一致するか確認
      if (quest.giver !== giverId) return false;

      // すでに開始、完了、失敗したクエストは除外
      if (
        state.questJournal?.activeQuests?.some(q => q.id === quest.id) ||
        state.questJournal?.completedQuests?.includes(quest.id) ||
        state.questJournal?.failedQuests?.includes(quest.id)
      ) {
        return false;
      }

      // レベル要件をチェック
      if (quest.requiredLevel && state.character.level < quest.requiredLevel) {
        return false;
      }

      // 前提クエストをチェック
      if (quest.requiredQuestIds && quest.requiredQuestIds.length > 0) {
        const allRequiredCompleted = quest.requiredQuestIds.every(reqId =>
          state.questJournal?.completedQuests?.includes(reqId)
        );
        if (!allRequiredCompleted) return false;
      }

      // カスタム解放条件をチェック
      if (quest.unlockCondition && typeof quest.unlockCondition === 'function') {
        return quest.unlockCondition(state);
      }

      return true;
    });
  };

  questUtils.getCompletableQuestsForGiver = (giverId) => {
    // アクティブなクエストで、全目標達成済みで依頼者が一致するものを取得
    return state.questJournal?.activeQuests?.filter(quest => {
      // 全目標が完了しているか確認
      const allObjectivesComplete = quest.objectives.every(obj => obj.isCompleted);
      if (!allObjectivesComplete) return false;

      // クエストデータを取得して依頼者を確認
      const questData = QUEST_DATABASE.find(q => q.id === quest.id);
      return questData && questData.giver === giverId;
    }) || [];
  };

  questUtils.getQuestGiver = (giverId) => {
    // 依頼者IDに基づいて依頼者情報を返す
    const givers = {
      'tavernGirl': { name: 'リンネ', title: '酒場の看板娘', location: 'tavern' },
      'weaponShopGirl': { name: 'マリカ', title: '武器職人', location: 'weaponShop' },
      'armourShopGirl': { name: 'セラ', title: '防具職人', location: 'armourShop' }
    };

    return givers[giverId] || { name: '不明', title: '', location: 'square' };
  };
  
  questUtils.getLocationName = (locationId) => {
    switch (locationId) {
      case 'square': return '街の広場';
      case 'tavern': return '酒場「冒険者の休息」';
      case 'weaponShop': return '武器屋「鋼の意志」';
      case 'armourShop': return '防具屋「頑丈な盾」';
      default: return locationId;
    }
  };

  /**
   * ショップ名を取得するヘルパー関数
   * @param {string} shopId - ショップID
   * @returns {string} ショップ名
   */
  questUtils.getShopName = (shopId) => {
    const shopNames = {
      weapons: '武器屋「鋼の意志」',
      armours: '防具屋「頑丈な盾」'
    };
    return shopNames[shopId] || shopId;
  };

  return questUtils;
};

export default createQuestUtils;
