// クエスト関連のユーティリティ関数
export const getInitialQuests = () => {
  return QUEST_DATABASE.filter(quest => 
    !quest.requiredQuestIds || quest.requiredQuestIds.length === 0
  );
};

export const createQuestProgress = (quest) => {
  return {
    id: quest.id,
    title: quest.title,
    progress: quest.objectives.reduce((acc, obj) => {
      acc[obj.id] = false;
      return acc;
    }, {})
  };
}; 
