// src/pages/TownPage.js
import { useState } from 'react';
import {useLocation, useNavigate} from 'react-router-dom';
import { useGame } from '../context/GameContext';
import ResponsivePageLayout from '../components/layout/ResponsivePageLayout';
import EquipmentMenu from '../components/town/EquipmentMenu';
import SaveLoadMenu from '../components/game/SaveLoadMenu';
import { getLocationName } from '../utils/uiHelpers';

// インポートする施設コンポーネント
import TownSquare from '../components/town/TownSquare';
import Tavern from '../components/town/Tavern';
import WeaponShop from '../components/town/WeaponShop';
import ArmourShop from '../components/town/ArmourShop';

const TownPage = () => {
  const { state, changeTownLocation, log } = useGame();
  const [showSaveMenu, setShowSaveMenu] = useState(false);
  const navigate = useNavigate();
  const location = useLocation();

  const currentLocation = state.town.currentLocation;

  // Handle quest navigation
  const handleQuestsNavigation = () => {
    sessionStorage.setItem('questReturnPath', location.pathname);
    navigate('/quests');
  };

  // ダンジョン選択画面に移動
  const handleGoToDungeon = () => {
    navigate('/dungeon-select');
  };

  // 町の場所を変更
  const handleLocationChange = (location) => {
    changeTownLocation(location);
    // 広場に移動した時
    if (location === 'square') {
      log('街の広場に戻りました。次の冒険に備えましょう。');
    }
  };

  // 現在の場所に応じたコンポーネントを表示
  const renderLocationContent = () => {
    switch (currentLocation) {
      case 'square':
        return <TownSquare onLocationChange={handleLocationChange} onDungeonSelect={handleGoToDungeon} />;
      case 'tavern':
        return <Tavern onBack={() => handleLocationChange('square')} />;
      case 'weaponShop':
        return <WeaponShop onBack={() => handleLocationChange('square')} />;
      case 'armourShop':
        return <ArmourShop onBack={() => handleLocationChange('square')} />;
      default:
        return <div>不明な場所です</div>;
    }
  };

  // Menu button
  const actionButtons = (
    <>
      <button
          onClick={() => handleQuestsNavigation()}
          className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded transition-colors min-w-[90px] md:min-w-[120px]"
      >
        クエスト
      </button>
      <button
          onClick={() => setShowSaveMenu(true)}
          className="bg-indigo-700 hover:bg-indigo-600 text-white py-2 px-4 rounded transition-colors min-w-[90px] md:min-w-[120px]"
      >
        メニュー
      </button>
    </>
  );

  // Sidebar content
  const sidebarContent = (
      <EquipmentMenu/>
  );

  return (
      <>
        <ResponsivePageLayout
            title={getLocationName(currentLocation)}
            actions={actionButtons}
            sidebar={sidebarContent}
        >
          {renderLocationContent()}
        </ResponsivePageLayout>

        {/* セーブ/ロードメニュー */}
        {showSaveMenu && (
            <SaveLoadMenu onClose={() => setShowSaveMenu(false)} />
        )}
      </>
  );
};

export default TownPage;
