// src/components/game/actions/ActionButton.js
import React from 'react';
import Button, { BUTTON_TYPES } from '../../ui/Button';

// アクションのタイプに基づいてボタンタイプを決定
const getButtonType = (actionType) => {
  switch (actionType) {
    case 'attack': return BUTTON_TYPES.DANGER;
    case 'magic': return BUTTON_TYPES.PRIMARY;
    case 'negotiate': return BUTTON_TYPES.SECONDARY;
    case 'explore': return BUTTON_TYPES.PRIMARY;
    case 'rest': return BUTTON_TYPES.SUCCESS;
    case 'next': return BUTTON_TYPES.NEUTRAL;
    default: return BUTTON_TYPES.NEUTRAL;
  }
};

// アクションボタン
const ActionButton = ({ action, label, onClick, disabled = false, icon = null }) => {
  return (
    <Button
      type={getButtonType(action)}
      onClick={onClick}
      disabled={disabled}
      icon={icon}
      className="w-full"
    >
      {label}
    </Button>
  );
};

export default ActionButton;
