// src/context/utils/dungeonUtils.js
import { ACTION_TYPES } from '../actions';
import { DUNGEONS } from '../../data/dungeons';
import { QUEST_DATABASE } from '../../data/quests';
import { findItemById } from "../../data/itemDatabase";

/**
 * Create dungeon utility functions
 * @param {Object} state - Current game state
 * @param {Function} dispatch - Reducer dispatch function
 * @param {Object} utils - Utility functions
 * @returns {Object} Dungeon utility functions
 */
const createDungeonUtils = (state, dispatch, utils) => {
  const { log, gainXP, updateQuestObjectivesByTrigger } = utils;

  // 先に dungeonUtils オブジェクトを宣言
  const dungeonUtils = {};

  /**
   * ダンジョンの状態を更新
   * @param {Object} updates - ダンジョン状態の更新内容
   */
  dungeonUtils.updateDungeon = (updates) => {
    dispatch({
      type: ACTION_TYPES.UPDATE_DUNGEON,
      payload: updates
    });
  };
  
  /**
   * ダンジョンを選択して開始
   * @param {string} type - 開始するダンジョンのタイプ
   * @returns {boolean} ダンジョン開始が成功したかどうか
   */
  dungeonUtils.selectDungeon = (type) => {
    const dungeonData = DUNGEONS[type];
    
    if (!dungeonData) {
      log(`エラー: ダンジョン情報が見つかりません。`, 'failure');
      return;
    }
    
    dispatch({
      type: ACTION_TYPES.UPDATE_DUNGEON,
      payload: {
        type: dungeonData.id,
        level: 1,
        clearedRooms: 0,
        roomsToNextLevel: 5,
        enemyPresent: false,
        currentEnemy: null,
        treasurePresent: false,
        treasureOpened: false,
        trapPresent: false,
        currentTrap: null,
        isBossFloor: false
      }
    });

    log(`${dungeonData.name}の探索を開始しました！`, 'success');
    
    // ダンジョン探索に関連するクエスト目標を更新
    if (updateQuestObjectivesByTrigger) {
      updateQuestObjectivesByTrigger('dungeon_explore');
    }
    
    return true;
  };
  
  /**
   * 次のダンジョン階層へ進む
   */
  dungeonUtils.nextDungeonLevel = () => {
    const nextLevel = state.dungeon.level + 1;
    const dungeonData = DUNGEONS[state.dungeon.type];

    // 最大レベルチェック
    if (nextLevel > dungeonData.maxLevel) {
      log('これ以上先には進めないようだ...', 'warning');
      return;
    }

    const isBossFloor = dungeonData.bossLevels.includes(nextLevel);

    dispatch({
      type: ACTION_TYPES.UPDATE_DUNGEON,
      payload: {
        level: nextLevel,
        clearedRooms: 0,
        roomsToNextLevel: isBossFloor ? 1 : Math.floor(5 + nextLevel * 0.5),
        enemyPresent: false,
        currentEnemy: null,
        treasurePresent: false,
        treasureOpened: false,
        trapPresent: false,
        currentTrap: null,
        isBossFloor
      }
    });

    if (isBossFloor) {
      log(`第${nextLevel}層に到達した。強大な気配を感じる...`, 'warning');
    } else {
      log(`第${nextLevel}層に到達した。`);
    }
    
    // クエスト進行
    if (updateQuestObjectivesByTrigger) {
      updateQuestObjectivesByTrigger('dungeon_level_up', { level: nextLevel });
    }
  };
  
  /**
   * ダンジョン攻略完了処理
   * @param {string} type - 完了したダンジョンのタイプ
   */
  dungeonUtils.completeDungeon = (type) => {
    const dungeonData = DUNGEONS[type];
    
    if (!dungeonData) {
      log(`エラー: ダンジョン情報が見つかりません。`, 'failure');
      return;
    }
    
    // 基本XP報酬を計算（ダンジョンレベルに応じて）
    let baseXp = dungeonData.maxLevel * 15;
    
    // 報酬の付与
    const rewards = dungeonData.completion?.reward;
    if (rewards) {
      // ゴールド報酬
      if (rewards.gold) {
        dispatch({
          type: ACTION_TYPES.UPDATE_CHARACTER,
          payload: {
            gold: state.character.gold + rewards.gold
          }
        });
        log(`報酬として ${rewards.gold}ゴールドを獲得しました！`, 'success');
      }
      
      // アイテム報酬
      if (rewards.items && rewards.items.length > 0) {
        rewards.items.forEach(itemId => {
          const item = findItemById(itemId);
          if (item) {
            dispatch({
              type: ACTION_TYPES.ADD_TO_INVENTORY,
              payload: item
            });
            log(`報酬として ${item.name}を獲得しました！`, 'success');
          }
        });
      }
      
      // 経験値報酬があれば追加（レベルに応じて）
      if (rewards.xp) {
        baseXp += rewards.xp;
      }
    }
    
    // 経験値を付与
    if (gainXP && baseXp > 0) {
      gainXP(baseXp);
    }
    
    // 新しいうわさの解放
    if (dungeonData.completion?.revealed?.rumors) {
      dungeonData.completion.revealed.rumors.forEach(rumorId => {
        dispatch({
          type: ACTION_TYPES.ADD_RUMOR,
          payload: {
            id: rumorId,
            title: `${dungeonData.name}の秘密`,
            text: `${dungeonData.name}の奥深くで発見した情報。`
          }
        });
      });
      
      log(`新しいうわさが酒場で聞けるようになりました！`, 'success');
    }
    
    // 攻略済みダンジョンリストに追加
    dispatch({
      type: ACTION_TYPES.UPDATE_CHARACTER,
      payload: {
        completedDungeons: [...state.character.completedDungeons, type]
      }
    });
    
    log(`${dungeonData.name}の攻略を完了しました！おめでとうございます！`, 'critical');
    
    // ダンジョンクリア状態を設定
    dispatch({
      type: ACTION_TYPES.UPDATE_DUNGEON,
      payload: {
        isCleared: true,
        clearRewards: rewards
      }
    });
    
    // ボス撃破に関連するクエスト目標を更新
    state.questJournal?.activeQuests?.forEach(quest => {
      quest.objectives.forEach(objective => {
        // まだ完了していない目標のみ処理
        if (!objective.isCompleted) {
          const questData = QUEST_DATABASE.find(q => q.id === quest.id);
          if (!questData) return;
          
          // ボス撃破やダンジョンクリアに関連する目標IDを確認
          // 目標IDでの明示的な判定（descriptionによる曖昧な判定は行わない）
          if (objective.id === 'kill_boss' ||
              objective.id === 'defeat_boss' ||
              objective.id === 'clear_dungeon' ||
              objective.id.includes('boss') ||
              objective.id.includes('dungeon_clear')) {
            
            // 明示的にダンジョンタイプを指定している場合はそれも確認
            const targetDungeonType = objective.targetDungeon;
            if (!targetDungeonType || targetDungeonType === type) {
              dispatch({
                type: ACTION_TYPES.UPDATE_QUEST_OBJECTIVE,
                payload: { 
                  questId: quest.id, 
                  objectiveId: objective.id, 
                  isCompleted: true 
                }
              });
              
              const objData = questData.objectives.find(o => o.id === objective.id);
              const description = objData?.description || 'ボス撃破';
              log(`クエスト「${questData.title}」: ${description}を完了しました。`, 'success');
            }
          }
        }
      });
    });
  };

  /**
   * 宝箱を開け、その中身を処理する
   */
  dungeonUtils.openTreasure = () => {
    const treasureIndex = Math.floor(Math.random() * 3);
    let treasure;
    
    switch (treasureIndex) {
      case 0:
        treasure = { name: "回復薬", effect: "HPが10回復した！" };
        dispatch({
          type: ACTION_TYPES.UPDATE_CHARACTER,
          payload: { 
            hp: Math.min(state.character.hp + 10, state.character.maxHp) 
          }
        });
        break;
      case 1:
        treasure = { name: "魔力の結晶", effect: "MPが10回復した！" };
        dispatch({
          type: ACTION_TYPES.UPDATE_CHARACTER,
          payload: { 
            mp: Math.min(state.character.mp + 10, state.character.maxMp) 
          }
        });
        break;
      case 2:
        const xpGain = 5 + state.dungeon.level * 2;
        treasure = { name: "古代の遺物", effect: `${xpGain}の経験値を獲得した！` };
        
        // 経験値を付与
        if (gainXP) {
          gainXP(xpGain);
        }
        break;
      default:
        treasure = { name: "謎の宝箱", effect: "特に効果はなかった..." };
    }
    
    // 宝箱を開けた後、部屋をクリアとして記録
    dispatch({
      type: ACTION_TYPES.UPDATE_DUNGEON,
      payload: { 
        clearedRooms: state.dungeon.clearedRooms + 1,
        treasureOpened: true
      }
    });
    
    // クエスト進行
    if (updateQuestObjectivesByTrigger) {
      updateQuestObjectivesByTrigger('open_treasure', { treasureType: treasure.name });
    }
    
    return treasure;
  };

  /**
   * @deprecated この関数はopenTreasureに置き換えられました
   * Create a treasure for the player to find
   */
  dungeonUtils.createTreasure = () => {
    return dungeonUtils.openTreasure();
  };

  return dungeonUtils;
};

export default createDungeonUtils;
