// src/data/characters.js

import { tavernGirlData } from './conversations/tavernGirl';
import { weaponShopGirlData } from './conversations/weaponShopGirl';
import { armourShopGirlData } from './conversations/armourShopGirl';

const CHARACTER_MAP = {
  tavernGirl: tavernGirlData,
  weaponShopGirl: weaponShopGirlData,
  armourShopGirl: armourShopGirlData
};

/**
 * キャラクターIDからキャラクター名を取得する関数
 * @param {string} characterId - キャラクターID
 * @returns {string} - キャラクター名
 */
export const getCharacterInfo = (characterId) => {
  const character = CHARACTER_MAP[characterId];
  if (!character) return characterId;
  
  return `${character.name}${character.title ? ` - ${character.title}` : ''}`;
};